import moment from 'moment'
import _ from 'lodash'
import React from 'react';
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import awardIcon from 'icons/award.png';
// import wedexLogoVertical from 'icons/wedexLogoVertical.svg';
import { Icon } from 'antd-mobile';

class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
    this.setState({
    })
  }

  render() {
    let { appState: {awardInfo}, dexAccount, closeAward, markRead } = this.props
    if (!awardInfo.visible) return <></>
    return (
      <>
        <Flex variant="flexCenter" sx={{
          position: 'fixed',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          margin: 'auto',
          backgroundImage: `url(${awardIcon})`,
          backgroundSize: 'contain',
          backgroundPosition: 'center',
          width: `${375 * 0.01}rem`,
          height: `${515 * 0.01}rem`,
          flexDirection: 'column',
          justifyContent: 'flex-start',
          zIndex: 999
        }} >
          {/* <Image src={awardIcon} width={300} /> */}
          {/* <Image mt={262} src={wedexLogoVertical} size={45} /> */}
          <Text fontSize="f2" mt={'2rem'} color="black" fontWeight='bold'></Text>
          <Text fontSize="f5" mt={'s2'} color="red" fontWeight='bold'>{awardInfo.amount}{awardInfo.unit}</Text>
          <Flex variant="flexCenter" color="hint" sx={{
            // borderTop: '1px dashed #646478',
            flexDirection: 'column'
          }}>
            <Text fontSize="f1" mt={'s2'}>:</Text>
            <Text fontSize="f1" mt={0}>{moment(awardInfo.createdAt).format('YYYY-MM-DD HH:mm:ss')}</Text>
            <Text mt={'1.4rem'}  color="border">24</Text>
            <Text mt={0}  color="border">{`${_.take(dexAccount.address, 10).join('')}......${_.takeRight(dexAccount.address, 10).join('')}`}</Text>
          </Flex>
          <Box sx={{
              position: 'absolute',
              right: `0.8rem`,
              top: `0.8rem`,
              // border: '1px solid #646478',
              borderRadius: '50%',
              size: 30,
              // padding: '3px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              cursor: 'pointer'
            }} onClick={async () => {
              await markRead([awardInfo.id])
              closeAward()
            }}>
            {/* <Icon type="close" style={{ fontSize: '15px', width: '15px', height: '15px'}}/> */}
            {/* <Svg style={{color: "#646478"}} size=".2rem" icon="icon-shanchu" /> */}
            <Icon type='cross-circle' />
          </Box>
        </Flex>
        <Box sx={{
          position: 'fixed',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          margin: 'auto',
          backgroundColor: `rgba(0, 0, 0, 0.88)`,
          zIndex: 998
        }} ></Box>
      </>
    )
  }
}

const mapStateToProps = ({appState, dexAccount}) => {
  return {
    appState: appState,
    dexAccount: dexAccount,
  };
};

const mapDispatchToProps = ({appState, award}) => {
  return {
    closeAward: appState.closeAward,
    markRead: award.markRead
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget);