
import React from 'react'
import { ActivityIndicator } from 'antd-mobile';

export default ({
  fontSize = 30,
  color = 'white',
  spinning,
  children,
  ...props
}) => {
  return (
    <>
      <div style={{ display: spinning ? 'unset' : 'none' }} {...props}>
        <ActivityIndicator animating />
      </div>
      <div style={{opacity: spinning ? 0 : 1}}>{ children }</div>
    </>
  )
}