import React from 'react';
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import { InputItem } from 'antd-mobile'
import { Svg } from 'components'
import { renderIf } from 'utils'

const InputWidget = ({label, value, onChange = () => {}, placeholder, onClick = () => {}, type = 'input', disabled = true}) => {
  return (
    <Flex mt="s4" variant="flexYCenter" sx={{
      padding: "s2",
      border: `1px solid #F0F0F0`,
      width: '100%',
      height: '.46rem',
      borderRadius: "3px",
    }} onClick={onClick}>
      <Box color="c2" width=".55rem" pr=".08rem" sx={{
        borderRight: `1px solid #F0F0F0`,
        textAlign: 'center',
      }}>{label}</Box>

      {
        disabled ? 
          <Box px="s2" flex={1}>{value}</Box>
        :
          <InputItem
            style={{fontSize: '.14rem', lineHeight: '.2rem', color: '#646464'}}
            type="digit"
            size="small"
            value={value}
            onChange={onChange}
          ></InputItem>
      }
      
      {
        renderIf(type === 'select')(
          <Svg color="#c8c8c8" icon="icon-xiala-copy" size=".25rem"></Svg>
        )
      }
    </Flex>
  )
}

export default InputWidget