import _ from 'lodash'
import React from 'react';
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { ActionSheet, List, WhiteSpace } from 'antd-mobile'
import { Svg } from 'components'
import { DepthBg } from 'widgets/Common'
import utils, { renderIf } from 'utils'

const lineHeight = .27
class Depth extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
  }
  
  componentWillUnmount() {
  }

  render() {
    let { markets, depth, trade: { latestPrice, variation }, currentPrecision, rate } = this.props

    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let {asks, bids} = depth
    asks = _.takeRight(asks, 5).reverse()
    bids = _.take(bids, 5)

    let max = _.maxBy([...asks, ...bids], item => Number(item.size))
    let maxSize = max ? max.size : 100
    return (
      <Box px="mainPadding">
        <Flex color="c2" height=".3rem" variant="flexYCenter" lineHeight={`${lineHeight}rem`}>
          <Box>{currentMarket.baseToken}</Box>
          <Box>{currentMarket.quoteToken}</Box>
          <Box>{currentMarket.baseToken}</Box>
        </Flex>

        <Box minHeight={`${5*lineHeight}rem`}>
          {
            _.range(5).map(index => {
              let ask = asks[index]
              let bid = bids[index]
              return (
                <Flex key={index} height=".3rem" onClick={() => {}} variant="flexYCenter" lineHeight={`${lineHeight}rem`}>
                  {
                    bid ?
                      <Flex variant="flexYCenter" justifyContent="space-between" flex={1} sx={{
                        position: "relative",
                        height: '100%',
                      }}>
                        <Text sx={{ zIndex:1 }}>{bid.size}</Text>
                        <Text sx={{ zIndex:1 }} color="c4" pr="s1">{bid.price}</Text>
                        <DepthBg gap bg="c7" percentage={bid.size / maxSize}></DepthBg>
                      </Flex>
                    : <Box flex={1}></Box>
                  }
                  {
                    ask ?
                      <Flex variant="flexYCenter" justifyContent="space-between" flex={1} sx={{
                        position: "relative",
                        height: '100%'
                      }}>
                        <Text sx={{ zIndex:1 }} color="c3" pl="s1">{ask.price}</Text>
                        <Text sx={{ zIndex:1 }}>{ask.size}</Text>
                        <DepthBg align="left" gap bg="c6" percentage={bid.size / maxSize}></DepthBg>
                      </Flex>
                    : <Box flex={1}></Box>
                  }
                </Flex>
              )
            })
          }
        </Box>
      </Box>
    )
  }
}

const mapStateToProps = ({ markets, depth, trade, rate }) => {
  return {
    markets,
    depth,
    trade,
    rate,
    precisions: () => {
      let market = markets.currentMarket
      if (!market) return []

      let { pricePrecision, maxLevel } = market
      return _.range(pricePrecision, pricePrecision - maxLevel)
    },
    currentPrecision: () => {
      if (!markets.currentMarket) return 0
      return markets.currentMarket.pricePrecision - depth.level
    }
  };
};

const mapDispatchToProps = ({ depth, placeOrder }) => {
  return {
    setLevel: depth.setLevel,
    scrollSellTable: depth.scrollSellTable,
    updatePlaceOrder: placeOrder.update
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Depth);