import _ from 'lodash'
import React, { forwardRef, useEffect, useState, useImperativeHandle, useRef, Fragment } from 'react';
import { withRouter } from "react-router-dom"
import { connect } from 'react-redux'
import { useIntl, FormattedMessage } from 'react-intl';
import { ActionSheet, List } from 'antd-mobile';
import { BillPreCheck } from 'widgets/Common'
import { UpsertPasswordModal } from 'widgets/Dialog'

const Item = List.Item;
const Brief = Item.Brief;

const Widget = (props) => {
  const billPreCheck = useRef(null)
  const intl = useIntl()

  const settings = [
    {
      title: '',
      items: [
        {
          title: intl.formatMessage({id: 'navBar.lang'}), handler: ({ updatePersistData }) => {
            let languages = [
              { title: '', value: 'cn' },
              { title: 'English', value: 'us' },
              { title: '', value: 'kr' },
              { title: '', value: 'ru' },
              { title: '', value: 'jp' },
            ]
            let options = _.map(languages, language => language.title)
            ActionSheet.showActionSheetWithOptions(
              {
                options: [...options, intl.formatMessage({id: 'cancel'})],
                cancelButtonIndex: options.length,
                maskClosable: true,
              },
              index => {
                let language = languages[index]
                if (!language) return
                updatePersistData({locale: language.value})
              }
            )
          }
        },
      ]
    },
    {
      title: intl.formatMessage({id: 'navBar.help'}),
      items: [
        { title: intl.formatMessage({id: 'navBar.help.notice'}), handler: () => {} },
        { title: intl.formatMessage({id: 'navBar.help.tutorial'}), handler: () => {} },
        { title: intl.formatMessage({id: 'navBar.help.faq'}), handler: () => {} },
        // { title: '', handler: () => {} },
        { title: intl.formatMessage({id: 'navBar.help.about'}), handler: () => {} },
      ]
    },
    {
      title: '',
      items: [
        { title: intl.formatMessage({id: 'navBar.mine.award'}), handler: ({ history, locale }) => history.push(`/${locale}/setting/awardRecord`) },
      ]
    },
    {
      title: '',
      items: [
        { title: intl.formatMessage({id: 'navBar.mine.apikey'}), handler: ({ history, locale }) => history.push(`/${locale}/setting/apikey`) },
      ]
    },
    {
      title: '',
      items: [
        { title: intl.formatMessage({id: 'navBar.mine.clean'}), handler: ({history, logout}) => {
          logout()
        }},
        { title: intl.formatMessage({id: 'navBar.mine.resetPwd'}), handler: ({billPreCheck}) => {
          billPreCheck.current.checkSignedIn()
          UpsertPasswordModal()
        }},
      ]
    },
  ]

  return (
    <div>
      {
        _.map(settings, list => (
          <List renderHeader={() => {list.title}}>
            {
              _.map(list.items, (item, index) => (
                <Item
                  key={item.title}
                  arrow="horizontal"
                  onClick={() => item.handler({...props, billPreCheck})}
                >{item.title}</Item>
              ))
            }
          </List>
        ))
      }
      <BillPreCheck ref={billPreCheck}></BillPreCheck>
    </div>

  )
}

const mapStateToProps = ({ persistData }) => {
  return {
    locale: persistData.locale,
  }
}

const mapDispatchToProps = ({ dexAccount, persistData }) => {
  return {
    logout: dexAccount.logout,
    updatePersistData: persistData.update
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Widget))

