import _ from 'lodash'
import moment from 'moment';
import clipboard from 'clipboard';
import React, { forwardRef, useEffect, useState, useImperativeHandle, Fragment } from 'react';
import { withRouter } from "react-router-dom"
import { useIntl, FormattedMessage } from 'react-intl';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Button,
  Text
} from 'rebass'
import { connect } from 'react-redux'
import { Page, Svg } from 'components'
import { TabNav } from 'widgets'
import utils, { renderIf } from 'utils';

const shadowWhite = 'rgba(255, 255, 255, 0.4)'
const buttonStyle = {
  fontSize: "f1",
  fontWeight: "semibold",
  border: `1px solid ${shadowWhite}`,
  width: '.75rem',
  height: '.26rem',
  borderRadius: "3px",
  textAlign: 'center'
}

let OverViewWidget = ({
  history,
  dexAccount,
  balance,
  walletBalance,
  persistData: { locale }
}) => {

  const intl = useIntl()
  let [visible, setVisible] = useState(true)
  useEffect(() => {
    new clipboard('.copyAddress')
  }, [])

  let balanceTotal = _.reduce(balance.list, (total, item) => {
    let usd = utils.asLegalMoney(Number(item.totalAmount), item.token.symbol, 'USD')
    return total + Number(usd)
  }, 0)
  let walletBalanceTotal = _.reduce(walletBalance.list, (total, item) => {
    let usd = utils.asLegalMoney(Number(item.amount), item.symbol, 'USD')
    return total + Number(usd)
  }, 0)
  let total = balanceTotal + walletBalanceTotal

  return (
    <Flex px="mainPadding" py="s3" color="white" bg="c5" flexDirection="column">
      <Flex>
        <Text fontSize="f2" lineHeight="lh2" mr="s2" fontWeight="bold">
          <FormattedMessage id='mine.balance.overview.total' />
        </Text>
        <Svg onClick={() => setVisible(!visible)} icon={visible ? "icon-xianshikejian" : "icon-yincangbukejian"} size=".2rem"></Svg>
        <Box flex={1}></Box>
        <Svg onClick={() => history.push(`/${locale}/setting`)} icon="icon-shezhi" size=".16rem"></Svg>
      </Flex>
      <Flex variant="flexYCenter" justifyContent="flex-start" mt="s2">
        {
          renderIf(visible)(
            <>
              <Text fontSize="f5" lineHeight="lh5" fontWeight="bold">{utils.toTokenPrecision(total, 'USDT')}</Text>
              <Text fontSize="f5" lineHeight="lh5" fontWeight="bold" ml="s2">USD</Text>
              <Text fontSize="f2" lineHeight="lh2" ml="s3" color={shadowWhite}> {utils.asLegalMoney(total, 'USD', 'CNY')}</Text>
            </>
          )
        }
        {
          renderIf(!visible)(
            <Text fontSize="f3" lineHeight="lh3" fontWeight="bold">************</Text>
          )
        }
      </Flex>
      <Flex mt="s4">
        <Flex variant="flexCenter" sx={buttonStyle} onClick={() => history.push(`/${locale}/transfer/create`)}>
          <FormattedMessage id="mine.balance.operations.transfer" />
        </Flex>
        <Flex variant="flexCenter" ml="s3" sx={buttonStyle} onClick={() => history.push(`/${locale}/transfer/list`)}>
          <FormattedMessage id="mine.balance.operations.record" />
        </Flex>
      </Flex>
      <Flex color={shadowWhite} mt="s3" variant="flexYCenter">
        <Flex lineHeight="lh2" mr="s2">
          <Text mr={".08rem"}>{intl.formatMessage({id: 'mine.balance.overview.addr'})}: </Text>
          {_.take(dexAccount.address, 10).join('')}......{_.takeRight(dexAccount.address, 10).join('')}
        </Flex>
        <Text className="copyAddress" data-clipboard-text={dexAccount.address} style={{whiteSpace: "nowrap"}}>
          <FormattedMessage id="copy" />
        </Text>
      </Flex>
    </Flex>
  )
}
const OverView = withRouter(connect(({ dexAccount, balance, walletBalance, persistData }) => {
  return {
    dexAccount,
    balance,
    walletBalance,
    persistData,
  }
}, () => {
  return {}
})(OverViewWidget))

const ExchangeBalaneWidget = ({
  balance
}) => {
  const intl = useIntl()
  let balances = balance.list

  return (
    <Box px="mainPadding">
      <Flex height=".4rem" alignItems="center" justifyContent="space-between" color="c2" lineHeight="s1">
        <Box width="1.1rem">{intl.formatMessage({id: 'tokenName'})}</Box>
        <Box width="1.1rem" textAlign="right">{intl.formatMessage({id: 'mine.balance.exchange.list.header.frozenAmount'})}</Box>
        <Box width="1.1rem" textAlign="right">{intl.formatMessage({id: 'mine.balance.exchange.list.header.availAmount'})}</Box>
      </Flex>
      {
        _.map(balances, item => {
          let { tokenId, token, availAmount, frozenAmount, totalAmount } = item

          return (
            <Flex key={tokenId} height=".4rem" alignItems="center" justifyContent="space-between" sx={{
              borderTop: '1px solid',
              borderTopColor: 'border',
            }}>
              <Flex width="1.1rem">{token.symbol}<Text ml="s1" color="c2">{token.name}</Text></Flex>
              <Box width="1.1rem" textAlign="right">{utils.toTokenPrecision(frozenAmount, token.symbol)}</Box>
              <Box width="1.1rem" textAlign="right">{utils.toTokenPrecision(availAmount, token.symbol)}</Box>
            </Flex>
          )
        })
      }
    </Box>
  )
}
const ExchangeBalane = withRouter(connect(({ balance }) => {
  return {
    balance
  }
}, () => {
  return {}
})(ExchangeBalaneWidget))

const WalletBalaneWidget = ({
  walletBalance
}) => {
  const intl = useIntl()
  let walletBalances = walletBalance.list

  return (
    <Box px="mainPadding">
      <Flex height=".4rem" alignItems="center" justifyContent="space-between" color="c2" lineHeight="s1">
        <Box width="1.1rem">{intl.formatMessage({id: 'tokenName'})}</Box>
        <Box width="1.1rem" textAlign="right">{intl.formatMessage({id: 'mine.balance.wallet.list.header.amount'})}</Box>
      </Flex>
      {
        _.map(walletBalances, item => {
          let { symbol, name, amount } = item

          return (
            <Flex key={symbol} height=".4rem" alignItems="center" justifyContent="space-between" sx={{
              borderTop: '1px solid',
              borderTopColor: 'border',
            }}>
              <Flex width="1.1rem">{symbol}<Text ml="s1" color="c2">{name}</Text></Flex>
              <Box width="1.1rem" textAlign="right">{utils.toTokenPrecision(amount, symbol)}</Box>
            </Flex>
          )
        })
      }
    </Box>
  )
}
const WalletBalane = withRouter(connect(({ walletBalance }) => {
  return {
    walletBalance
  }
}, () => {
  return {}
})(WalletBalaneWidget))

const Widget = ({
  fetchBalances,
  fetchWalletBalances
}) => {
  const intl = useIntl()

  const tabs = [
    { text: intl.formatMessage({id: 'mine.balance.tab.wallet'}), value: 'walletBalance' },
    { text: intl.formatMessage({id: 'mine.balance.tab.exchange'}), value: 'exchangeBalance' },
  ]
  
  let [activeTab, setActiveTab] = useState(tabs[0].value)
  useEffect(() => {
    fetchBalances()
    fetchWalletBalances()
  }, [])

  return (
    <Page className="home-page">
      <Flex flexDirection="column">
        <OverView />
        <TabNav tabs={tabs} onChange={({value}) => {
          setActiveTab(value)
          if (value === 'exchangeBalance') {
            fetchBalances()
          } else {
            fetchWalletBalances()
          }
        }} />
        {
          renderIf(activeTab === 'exchangeBalance')(<ExchangeBalane/>)
        }
        {
          renderIf(activeTab === 'walletBalance')(<WalletBalane/>)
        }
      </Flex>
    </Page>
  )
}
const mapStateToProps = ({ dexAccount, balance, walletBalance, rate, markets }) => {
  return {
    dexAccount,
    balance,
    walletBalance,
    rate,
    markets
  };
};
const mapDispatchToProps = ({ balance, walletBalance, transfer, markets }) => {
  return {
    fetchBalances: balance.fetchBalances,
    fetchWalletBalances: walletBalance.fetchAllBalances,
    setCurrentTab: markets.setCurrentTab,
    setCurrentMarket: markets.setCurrentMarket,
  };
};
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Widget))

