import React, { Component, useEffect, useState } from 'react'
import { connect, Provider } from 'react-redux'
import { Switch, Route, Redirect } from "react-router-dom"
import { ActivityIndicator, Drawer } from 'antd-mobile';
import { injectIntl, FormattedMessage  } from 'react-intl'
import Login from 'views/Login'
import Home from 'views/Home'
import Pair from 'views/Pair'
import Market from 'views/Market'
import TransferList from 'views/transfer/TransferList'
import TransferCreate from 'views/transfer/TransferCreate'
import Setting from 'views/setting/Setting'
import Apikey from 'views/setting/Apikey'
import AwardRecord from 'views/setting/AwardRecord'
import { Toast } from 'utils'

const mapState = ({ persistData, appState }) => {
  return {
    locale: persistData.locale,
    market: persistData.market,
    notification: appState.notification,
    showMarketDrawer: appState.showMarketDrawer,
  }
}
const mapDispatch = ({ appState }) => {
  return {
    updateAppState: appState.update,
  }
}

@connect(mapState, mapDispatch)
@injectIntl
export default class Widget extends React.Component {

  componentWillReceiveProps(newProps) {
    let { intl } = this.props
    if (
      // 
      // newProps.notification.message !== this.props.notification.message && 
      newProps.notification.message
    ) {
      let { type, message, duration = 1 } = newProps.notification
      let typeMapping = {
        error: 'fail',
        warning: 'fail',
      }
      let toastType = typeMapping[type] || type
      // 
      // https://github.com/formatjs/react-intl/blob/master/docs/API.md#createintl
      Toast[toastType](intl.formatMessage({id: message}), duration)
    }
  }

  render() {
    let { locale, market, showMarketDrawer, updateAppState } = this.props
    return (
      // drawer
      // 1.  drawer  position  fixed Drawer children css 
      // 2. drawer  children drawer
      <Drawer
        position='left'
        sidebarStyle={{
          width: '80%',
          height: document.documentElement.clientHeight, background: 'white',
          display: 'flex',
          flexDirection: 'column'
        }}
        sidebar={<Pair />}
        open={showMarketDrawer}
        onOpenChange={open => updateAppState({showMarketDrawer: open})}
      >
        <Switch basename={locale}>
          <Route exact path="/" render={() => <Redirect to={`/cn/trade/${market}`} />} />
          <Route exact path="/trade" render={() => <Redirect to={`/cn/trade/${market}`} />} />

          <Route exact path="/:locale/trade/:market" component={Home} />
          <Route exact path="/:locale/login" component={Login} ></Route>
          <Route exact path="/:locale/pair" component={Pair} ></Route>
          <Route exact path="/:locale/market" component={Market} ></Route>
          <Route exact path="/:locale/transfer/list" component={TransferList} ></Route>
          <Route exact path="/:locale/transfer/create" component={TransferCreate} ></Route>
          <Route exact path="/:locale/setting" component={Setting} ></Route>
          <Route exact path="/:locale/setting/apikey" component={Apikey} ></Route>
          <Route exact path="/:locale/setting/awardRecord" component={AwardRecord} ></Route>
        </Switch>
      </Drawer>
    )
  }
}
