import _ from 'lodash'
import React, { Component } from 'react'
import { SearchBar, ListView, List, PullToRefresh } from 'antd-mobile'
import { injectIntl, FormattedMessage  } from 'react-intl'

const { Item } = List;

function MyBody(props) {
  return (
    <div className="am-list-body my-body">
      <span style={{ display: 'none' }}>you can custom body wrap element</span>
      {props.children}
    </div>
  )
}

class InfiniteScroll extends Component {
  constructor (props) {
    super(props)
    
    // const getRowData = (dataBlob, rowID) => dataBlob[rowID];

    const dataSource = new ListView.DataSource({
      // getRowData,
      rowHasChanged: (row1, row2) => {
        return row1 !== row2
      }
    })

    this.state = {
      dataSource,
      isLoading: true,
      hasMore: true,
      error: false,
      refreshing: true
    }
    this.pager = {
      // 1. 
      pageSize: 20,
      page: 1
    }
    // 
    this.disablePage = false
    this.dataBlobs = []
  }

  async componentDidMount () {
    this.disablePage = this.props.disablePage

    if (!this.props.disableAutoLoad) {
      this.reset()
    }
  }

  componentWillUnmount() {
  }

  _parseRetList (res) {
    if (this.props.parseRetList) {
      return this.props.parseRetList(res)
    }
    const {listKey} = this.props
    return res.data.List || res.data[listKey]
  }

  reset = async () => {
    await this.setState({
      isLoading: true,
      hasMore: true,
      error: false
    })
    this.pager = {
      pageSize: 20,
      page: 1
    }
    try {
      let res = await this._fetchDataByPage()
      this.dataBlobs = []
      this.setState({
        dataSource: this.state.dataSource.cloneWithRows(this.dataBlobs)
      })
      this._fetchSuccess(this._parseRetList(res))
    } catch (err) {
      this._fetchFailed()
    }
  }

  onEndReached = async (event) => {
    if (this.state.isLoading || !this.state.hasMore || this.disablePage) {
      return
    }
    this.setState({ isLoading: true });
    try {
      let res = await this._fetchDataByPage()
      this._fetchSuccess(this._parseRetList(res))
    } catch (err) {
      this._fetchFailed()
    }
  }

  _fetchDataByPage = async () => {
    return this.props.fetchData(this.pager)
  }

  _fetchSuccess = (list) => {
    if (_.isArray(list) && list.length > 0) {
      this.dataBlobs = this.dataBlobs.concat(list)
      this.setState({
        dataSource: this.state.dataSource.cloneWithRows(this.dataBlobs),
        isLoading: false,
      })
      this.pager.page++
    }
    else {
      this.setState({isLoading: false, hasMore: false})
    }
  }

  _fetchFailed = (payload) => {
    this.setState({isLoading: false, error: true})
  }

  // ListView
  refreshItem (index) {
    // listview this.dataBlobs array mutable  concat push[...]
    // DataSource dataBlobs+ds  copy  db db ds  rowHasChanged 
    // : cloneWithRows  _calculateDirtyArrays  mutable  ds_calculateDirtyArraysds dsrowHasChanged
    // renderItem item 
    //  https://github.com/facebook/react-native/blob/12a0045412644c2467efce8e83a86a6810a866d4/Libraries/CustomComponents/ListView/ListViewDataSource.js
    let newDataBlobs = [...this.dataBlobs]
    let newItem = _.cloneDeep(this.dataBlobs[index])
    newDataBlobs[index] = newItem
    this.setState({
      dataSource: this.state.dataSource.cloneWithRows(newDataBlobs),
    })
  }

  render () {
    const styles = {
      overflow: 'auto',
      width: '100%'
    }

    if (this.props.autoHeight) {
      styles.flex = 1
    } else {
      styles.height = this.props.height || 1000
    }

    let {renderEmptyList, renderItem, enableRefresh, onRefresh, intl, ...otherProps} = this.props

    let pullToRefresh = false
    if (enableRefresh) {
      pullToRefresh = (
        <PullToRefresh
          // refreshing={this.state.refreshing}
          onRefresh={onRefresh || this.reset}
        />
      )
    }
    return (
      <ListView
        ref={el => this.lv = el}
        dataSource={this.state.dataSource}
        pullToRefresh={pullToRefresh}
        renderFooter={function () {
          let hint
          if (this.state.isLoading) {
            return <div style={{ padding: 0, textAlign: 'center' }}>{intl.formatMessage({id: 'loading'})}</div>
          } else if (this.state.dataSource._cachedRowCount === 0) {
            if (renderEmptyList) {
              return renderEmptyList()
            } else {
              return <div style={{ padding: 0, textAlign: 'center' }}>{intl.formatMessage({id: 'noData'})}</div>
            }
          } else {
            // return <div style={{ padding: 0, textAlign: 'center' }}></div>
          }
        }.bind(this)}
        renderBodyComponent={() => <MyBody />}
        renderRow={renderItem}
        style={styles}
        pageSize={4}
        scrollRenderAheadDistance={1000}
        onEndReached={this.onEndReached}
        onEndReachedThreshold={10}
        // 2. 
        initialListSize={20}
        {...otherProps}
      />
    )
  }
}

// injectIntl  forwardRef
export default injectIntl(InfiniteScroll, { forwardRef: true })