import _ from 'lodash'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import numeral from 'numeral'
import moment from 'moment'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import styled from '@emotion/styled'
import { TextEllipsis } from 'widgets/Common'
import { ENUM_LANGUAGE_TYPE } from 'utils/enum'

const Line = styled(Flex)`
  justify-content: space-between;
  padding-top: .16rem;
`
const Label = styled(Text)`
  width: .6rem;
  color: #D2D2D2;
`
const Value = styled(TextEllipsis)`
  width: 2.8rem;
  text-align: right;
`
class TokenInfo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
  }

  render() {
    let { tokenInfo, persistData: { locale } } = this.props
    if (!tokenInfo.id) return <></>

    let lang = ENUM_LANGUAGE_TYPE[locale].getValue()
    let { name, fullname, marketValue, publishDate, rank, publishAmount, homepage, flowAmount, whitePaper, publishPrice, desc } = tokenInfo
    let descInLocale = desc && desc[lang] || ''

    return (
      <Box {...this.props} px={12} pt={14} color="#505050" variant='noScrollbar' bg="backgroundTint" overflowY='auto'>
        <Flex fontSize="f2">
          <Text>{name}</Text>
          <Text>{fullname}</Text>
        </Flex>
        <Flex fontSize={"f1"}>
          <Flex flexDirection="column">
            <Line>
              <Label><FormattedMessage id="tokenInfo.label.marketValue" /></Label>
              <Value>${numeral(marketValue).format('0,0')}</Value>
            </Line>
            <Line>
              <Label><FormattedMessage id="tokenInfo.label.rank" /></Label>
              <Value>{rank}</Value>
            </Line>
            <Line>
              <Label><FormattedMessage id="tokenInfo.label.homepage" /></Label>
              <Value color="#0064FF" sx={{cursor: 'pointer'}} onClick={() => window.open(homepage, '_blank')}>{homepage}</Value>
            </Line>
            <Line>
              <Label><FormattedMessage id="tokenInfo.label.whitePaper" /></Label>
              <Value color="#0064FF" sx={{cursor: 'pointer'}} onClick={() => window.open(whitePaper, '_blank')}>{whitePaper}</Value>
            </Line>
            <Line>
              <Label><FormattedMessage id="tokenInfo.label.publishDate" /></Label>
              <Value>{moment(publishDate).format('YYYY-MM-DD')}</Value>
            </Line>
            <Line>
              <Label><FormattedMessage id="tokenInfo.label.publishAmount" /></Label>
              <Value>{numeral(publishAmount).format('0,0')}</Value>
            </Line>
            <Line>
              <Label><FormattedMessage id="tokenInfo.label.flowAmount" /></Label>
              <Value>{numeral(flowAmount).format('0,0')}</Value>
            </Line>
            <Line>
              <Label><FormattedMessage id="tokenInfo.label.publishPrice" /></Label>
              <Value>{numeral(publishPrice).format('0,0')}</Value>
            </Line>
          </Flex>
        </Flex>
        <Flex pt={16} fontSize={"f1"} flexDirection="column">
          <Text color="#D2D2D2"><FormattedMessage id="tokenInfo.label.desc" /></Text>
          <Text my={16} lineHeight={'.2rem'}>{descInLocale}</Text>
        </Flex>
      </Box>
    )
  }
}

const mapStateToProps = ({persistData}) => {
  return { persistData }
};

const mapDispatchToProps = ({markets}) => {
  return {
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(TokenInfo));