import _ from 'lodash'
import numeral from 'numeral';
import React from 'react';
import { connect } from 'react-redux';
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { withRouter } from "react-router-dom"
import { ActionSheet, List, Toast } from 'antd-mobile'
import { Svg } from 'components'
import { CoinInput, BillPreCheck } from 'widgets'
import utils from 'utils'
const BigNumber = require('bignumber.js');

const percentPositins = [ 0.25, 0.5, 0.75, 1 ]
@injectIntl
class PlaceOrder extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      side: 'BUY',
      activeType: 'limitPrice'
    }
  }

  componentDidMount() {
  }

  onSelectType () {
    let { intl } = this.props

    const types = [
      { text: intl.formatMessage({id: 'placeOrder.tabs.limitPrice'}), value: 'limitPrice' },
      // { text: intl.formatMessage({id: 'placeOrder.tabs.marketPrice'}), value: 'marketPrice' },
    ]
    let options = types.map(t => t.text)
    ActionSheet.showActionSheetWithOptions(
      {
        options: [...options, intl.formatMessage({id: 'cancel'})],
        cancelButtonIndex: options.length,
        maskClosable: true,
      },
      index => {
        let type = types[index]
        if (!type) return
        this.setState({ activeType: type.value })
      }
    )
  }

  renderLimitBuyArea (min, available) {
    let { markets, persistData: { locale }, history, intl } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let { baseToken, quoteToken, pricePrecision, amountPrecision, totalPrecision } = currentMarket
    let { placeOrder: { buyPrice, buyAmount, buyTotal }, updatePlaceOrder, placeBuyOrder } = this.props
    let signined = utils.checkLogined()

    let error = null
    if (buyTotal) {
      min = Number(min)
      available = Number(available)
      // buyTotal
      // buyTotal = Number(buyTotal)
      let total = Number(buyTotal)
      if (total < min) {
        error = intl.formatMessage({id: 'placeOrder.hint.buyLessThanMin'}, { min, quoteToken })
      } else if (total > available) {
        error = intl.formatMessage({id: 'placeOrder.hint.moreThanAvalable'}, { available, token: quoteToken })
      }
    }

    return (
      <>
        <CoinInput
          label={intl.formatMessage({id: "placeOrder.input.label.buyPrice"})}
          unit={quoteToken}
          precision={pricePrecision}
          value={buyPrice}
          onChange={value => {
            let price = value
            let buyTotal = (new BigNumber(buyAmount || 0)).times(new BigNumber(price || 0))
            updatePlaceOrder({
              //  price 0 
              buyPrice: price,
              buyTotal: buyTotal.toFixed(totalPrecision)
            })
          }}
        >
        </CoinInput>
        <Text mt="s1" color="c2">{`$${utils.asLegalMoney(buyPrice, quoteToken, 'USD')}`}</Text>
        <CoinInput
          mt="s2"
          label={intl.formatMessage({id: "placeOrder.input.label.buySize"})}
          precision={amountPrecision}
          value={buyAmount}
          onChange={value => {
            let amount = value
            let total = (new BigNumber(amount || 0)).times(new BigNumber(buyPrice || 0))
            updatePlaceOrder({
              //  value 0 
              buyAmount: amount,
              buyTotal: total.toFixed(totalPrecision),
            })
          }}
          unit={baseToken}
        >
        </CoinInput>
        <CoinInput
          mt="s3"
          //  UI  stepper
          disabled
          label={intl.formatMessage({id: "placeOrder.input.label.total"})}
          precision={totalPrecision}
          max={available}
          min={min}
          unit={quoteToken}
          value={buyTotal}
          onChange={value => {
            let total = value
            let amount = buyPrice ? (new BigNumber(total || 0) / new BigNumber(buyPrice)) : 0
            updatePlaceOrder({
              buyAmount: total >= available ? _.floor(amount, amountPrecision) : _.ceil(amount, amountPrecision),
              //  total 0 
              buyTotal: total
            })
          }}
        >
        </CoinInput>
        <Flex mt="s1" variant="flexYCenter">
          <Text className="text-ellipsis" color="c2"><FormattedMessage id="placeOrder.title.available" />{quoteToken}: 11111111111111111{available}</Text>
          <Text flexShrink={0} color="c5" onClick={() => {
            history.push({ pathname: `/${locale}/transfer/create`, data: {
              initialSymbol: quoteToken,
              initialType: 'deposit',
            }})
          }}>
            <FormattedMessage id="placeOrder.operations.transfer" />
          </Text>
        </Flex>
        <Flex variant="flexYCenter" mt="s3" >
        {
          percentPositins.map(percent => (
            <Text key={percent} height="s3" lineHeight={'lh3'} bg="#f5f5f5" color="c2"
              sx={{
                width: '.43rem',
                borderRadius: ".01rem",
                textAlign: 'center'
              }}
              onClick={() => {
                let total = available * percent
                let amount = buyPrice ? (new BigNumber(total || 0) / new BigNumber(buyPrice)) : 0
                updatePlaceOrder({
                  // total === available
                  buyAmount: total >= available ? _.floor(amount, amountPrecision) : _.ceil(amount, amountPrecision),
                  //  total 0 
                  buyTotal: total >= available ? _.floor(total, totalPrecision) : _.ceil(total, totalPrecision),
                })
              }}
            >
              {numeral(percent).format('0%')}
            </Text>
          ))
        }
        </Flex>
        <BillPreCheck onSuccess={() => {
          if (buyTotal && !!error) {
            Toast.fail(error)
            return
          }
          placeBuyOrder()
        }}>
          <Flex variant="flexCenter" height=".35rem" mt="s3" sx={{
              flex: 1,
              borderRadius: ".02rem",
              bg: "c4",
              color: 'white'
            }}>
            {signined ? `${intl.formatMessage({id: 'buy'})}${baseToken}` : `${intl.formatMessage({id: 'placeOrder.hint.loginAndTrade'})}`}
          </Flex>
        </BillPreCheck>
      </>
    )
  }

  renderLimitSaleArea (min, available) {
    let { markets, persistData: { locale }, history, intl } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let { baseToken, quoteToken, pricePrecision, amountPrecision, totalPrecision } = currentMarket
    let { placeOrder: { sellPrice, sellAmount, sellTotal }, updatePlaceOrder, placeSellOrder } = this.props
    let signined = utils.checkLogined()

    let error = null
    if (sellAmount) {
      min = Number(min)
      available = Number(available)
      //  sellAmount 
      // sellAmount = Number(sellAmount)
      let amount = Number(sellAmount)
      if (amount < min) {
        error = intl.formatMessage({id: 'placeOrder.hint.saleLessThanMin'}, { min, baseToken })
      } else if (amount > available) {
        error = intl.formatMessage({id: 'placeOrder.hint.moreThanAvalable'}, { available, token: baseToken })
      }
    }

    return (
      <>
        <CoinInput
          label={intl.formatMessage({id: "placeOrder.input.label.sellPrice"})}
          unit={quoteToken}
          precision={pricePrecision}
          value={sellPrice}
          onChange={value => {
            let price = value
            let sellTotal = (new BigNumber(sellAmount || 0)).times(new BigNumber(price || 0))
            updatePlaceOrder({
              //  price 0 
              sellPrice: price,
              sellTotal: sellTotal.toFixed(totalPrecision)
            })
          }}
        >
        </CoinInput>
        <Text mt="s1" color="c2">{`$${utils.asLegalMoney(sellPrice, quoteToken, 'USD')}`}</Text>
        <CoinInput
          mt="s2"
          label={intl.formatMessage({id: "placeOrder.input.label.sellSize"})}
          precision={amountPrecision}
          value={sellAmount}
          onChange={value => {
            let amount = value
            let total = (new BigNumber(amount || 0)).times(new BigNumber(sellPrice || 0))
            updatePlaceOrder({
              //  amount 0 
              sellAmount: amount,
              sellTotal: total.toFixed(totalPrecision),
            })
          }}
          unit={baseToken}
        >
        </CoinInput>
        <CoinInput
          mt="s3"
          disabled
          label={intl.formatMessage({id: "placeOrder.input.label.total"})}
          precision={totalPrecision}
          max={available}
          min={min}
          unit={quoteToken}
          value={sellTotal}
          onChange={value => {
            let total = value
            let amount = sellPrice ? (new BigNumber(total || 0) / new BigNumber(sellPrice)) : 0
            updatePlaceOrder({
              sellAmount: total >= available ? _.floor(amount, amountPrecision) : _.ceil(amount, amountPrecision),
              //  total 0 
              sellTotal: total
            })
          }}
        >
        </CoinInput>
        <Flex mt="s1" variant="flexYCenter">
          <Text className="text-ellipsis" color="c2"><FormattedMessage id="placeOrder.title.available" />{baseToken}: {available}</Text>
          <Text flexShrink={0} color="c5" onClick={() => {
            history.push({ pathname: `/${locale}/transfer/create`, data: {
              initialSymbol: baseToken,
              initialType: 'deposit',
            }})
          }}>
            <FormattedMessage id="placeOrder.operations.transfer" />
          </Text>
        </Flex>
        <Flex variant="flexYCenter" mt="s3" >
        {
          percentPositins.map(percent => (
            <Text key={percent} height="s3" lineHeight={'lh3'} bg="#f5f5f5" color="c2"
              sx={{
                width: '.43rem',
                borderRadius: ".01rem",
                textAlign: 'center'
              }}
              onClick={() => {
                let amount = available * percent
                let total = (new BigNumber(amount || 0)).times(new BigNumber(sellPrice || 0))
                updatePlaceOrder({
                  //  amount 0 
                  sellAmount: _.floor(amount, amountPrecision),
                  sellTotal: total.toFixed(totalPrecision),
                })
              }}
            >
              {numeral(percent).format('0%')}
            </Text>
          ))
        }
        </Flex>
        <BillPreCheck onSuccess={() => {
          if (sellAmount && !!error) {
            Toast.fail(error)
            return
          }
          placeSellOrder()
        }}>
          <Flex variant="flexCenter" height=".35rem" mt="s3" sx={{
              flex: 1,
              borderRadius: ".02rem",
              bg: 'c3',
              color: 'white'
            }}>
            {signined ? `${intl.formatMessage({id: 'sell'})}${baseToken}` : `${intl.formatMessage({id: 'placeOrder.hint.loginAndTrade'})}`}
          </Flex>
        </BillPreCheck>
      </>
    )
  }

  getMinTradeValue (symbol) {
    let mapping = {
      'USDT': 2,
      'LRC': 100,
      'ETH': 0.01,
      'DKEY': 10,
    }
    return mapping[symbol] || 0
  }

  render() {
    let { side } = this.state
    let { markets: { currentMarket }, balance: { list: balances }, intl } = this.props
    if (!currentMarket) return <></>

    let { baseToken, quoteToken } = currentMarket
    let baseBalance = _.find(balances, balance => balance.token.symbol === baseToken)
    let quoteBalance = _.find(balances, balance => balance.token.symbol === quoteToken)
    let availBase = baseBalance ? baseBalance.availAmount : 0
    let availQuote = quoteBalance ? quoteBalance.availAmount : 0

    return (
      <Box {...this.props}>
        <Flex fontSize="f2" width="100%">
          <Flex variant="flexCenter" height=".3rem" sx={{
            flex: 1,
            borderRadius: ".02rem",
            bg: side === 'BUY' ? 'c4' : "#F0F0F0",
            color: side === 'BUY' ? 'white' : "c1",
          }} onClick={() => this.setState({side: 'BUY'})}>
            <FormattedMessage id="buy" />
          </Flex>
          <Flex variant="flexCenter" height=".3rem" sx={{
            flex: 1,
            borderRadius: ".02rem",
            bg: side === 'SELL' ? 'c3' : "#F0F0F0",
            color: side === 'SELL' ? 'white' : "c1",
          }} onClick={() => this.setState({side: 'SELL'})}>
            <FormattedMessage id="sell" />
          </Flex>
        </Flex>

        <Flex color="c1" py="mainPadding" width="fit-content" onClick={() => this.onSelectType()}>
          <Text>{intl.formatMessage({id: `placeOrder.tabs.${this.state.activeType}`})}{side === 'BUY' ? '' : ''}</Text>
          <Svg color="" icon="icon-xiala-copy" size=".16rem"></Svg>
        </Flex>
        {
          side === 'BUY' ?
            this.renderLimitBuyArea(this.getMinTradeValue(quoteToken), availQuote)
            :
            this.renderLimitSaleArea(this.getMinTradeValue(baseToken), availBase)
        }
      </Box>
    )
  }
}

const mapStateToProps = ({ placeOrder, markets, balance, dexAccount, rate, persistData }) => {
  return {
    placeOrder,
    markets,
    balance,
    dexAccount,
    rate,
    persistData
  };
};

const mapDispatchToProps = ({ placeOrder }) => {
  return {
    placeBuyOrder: placeOrder.placeBuyOrder,
    placeSellOrder: placeOrder.placeSellOrder,
    updatePlaceOrder: placeOrder.update
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(PlaceOrder))