import React, { forwardRef, useEffect, useState, useImperativeHandle, useRef } from 'react'
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import { QhStepper } from 'components';
import utils, { renderIf } from 'utils';
import { ActionSheet, List, Stepper } from 'antd-mobile'

const CoinInput = ({
  label,
  min = 0,
  max = Math.Infinity,
  precision = 6,
  value,
  onChange,
  disabled,
  unit,
  ...restProps
}) => {
  const [focused, setFocused] = useState(false)
  let hideLabel = focused || value

  return (
    <Flex {...restProps} sx={{ position: "relative" }}>
      <Text sx={{
        position: "absolute",
        left: disabled ? '.03rem' : ".3rem",
        top: '.08rem',
        // pr: 's1',
        [`p${disabled ? 'r' : 'l'}`]: 's1',
        fontWeight: 'medium',
        lineHeight: '.2rem',
        height: '.2rem',
        color: 'c2',
        // borderLeft: '1px solid #F0F0F0'
        [`border${disabled ? 'Right' : 'Left'}`]: '1px solid #F0F0F0'
      }}>{(hideLabel && !disabled) ? '' : label}</Text>

      {renderIf(disabled)(
        <Text sx={{
          border: '1px solid #F0F0F0',
          borderRadius: '3px',
          width: '100%',
          height: '.35rem',
          lineHeight: '.35rem',
          textAlign: "center",
          boxSizing: 'content-box'
        }}>{value}</Text>
      )}
      {renderIf(!disabled)(
        <QhStepper
          width={hideLabel ? '1.1rem' : '.55rem'}
          className="place-order-stepper"
          showNumber
          max={max}
          min={min}
          precision={precision}
          value={value}
          onKeyPress={(e)=>{
            // if (e.key === 'Backspace' || e.key === 'Delete') return
            if (!/^[0-9a-zA-Z]+$/.test(e.key)) return

            let value = e.target.value || ''
            let { selectionStart, selectionEnd } = e.currentTarget
            if (selectionStart !== selectionEnd) {
              //  else selectionStartselectionEnd
              value = value.slice(0, selectionStart) + e.key + value.slice(selectionEnd)
            } else {
              //  precision .
              value = value.slice(0, selectionStart) + e.key + value.slice(selectionStart)
            }

            let valueAsNumber = Number(value)
            if (!_.isNumber(valueAsNumber) || isNaN(valueAsNumber)) {
              e.preventDefault()
              return
            }

            // fix:  Number  0.0100000000 0 
            if (utils.getPrecision(value) > precision) {
              e.preventDefault()
              return
            }
          }}
          onChange={onChange}
          onBlur={() => setFocused(false)}
          onFocus={() => setFocused(true)}
        />
      )}

      <Text sx={{
        position: "absolute",
        right: disabled ? '.03rem' : ".3rem",
        top: '.08rem',
        [`p${disabled ? 'l' : 'r'}`]: 's1',
        fontWeight: 'medium',
        lineHeight: '.2rem',
        color: 'c2',
        borderRight: `${disabled ? 0 : 1}px solid #F0F0F0`
      }}>{unit}</Text>
    </Flex>
  )
}

const mapStateToProps = state => {
  return {
  };
};

const mapDispatchToProps = dispatch => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CoinInput);