import _ from 'lodash'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import numeral from 'numeral'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import { SearchBar } from 'antd-mobile'
import { Page, Svg } from 'components'
import { TabNav } from 'widgets'
import utils, { renderIf } from 'utils';

@injectIntl
class Pair extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fuzzy: '',
    }
  }

  componentDidMount() {
    // fetchMarkets market 
    setTimeout(() => {
      let { markets, setCurrentTab } = this.props
      setCurrentTab(markets.tabs[0])
    }, 300)
  }
  
  render() {
    let { history, persistData: { locale }, markets, setCurrentTab, setCurrentMarket, starMarket, addToHistory, clearHistory, updateAppState, intl } = this.props
    let { currentMarkets, searchHistory } = markets
    let { fuzzy } = this.state
    let pairs = _.chain(currentMarkets)
                  .filter(({market}) => _.includes(_.upperCase(market), _.upperCase(fuzzy)))
                  .value()

    return (
      <Page className="home-page">
        <Box px="mainPadding"  sx={{
          borderBottom: '1px solid #F0F0F0',
          pb: 's3'
        }}>
          <Box className="pair-searchbar">
            <SearchBar placeholder={intl.formatMessage({id: 'search'})} maxLength={8} onSubmit={value => {
              addToHistory(value)
              this.setState({ fuzzy: value })
            }} />
          </Box>
          <Flex alignItems="center" justifyContent="space-between">
            <Text py="s2" fontSize="f2"></Text>
            <Box onClick={() => clearHistory()}><Svg color="#0078FF" icon="icon-shanchu" size=".2rem"></Svg></Box>
          </Flex>
          <Flex mx="-.04rem" flexWrap="wrap">
          {
            _.map(searchHistory, item => (
              <Box flexBasis="25%" onClick={() => {
                this.setState({ fuzzy: item })
              }}>
                <Flex mx="s1" my="s1" bg="c5" height=".3rem" color="white" variant="flexCenter">{item}</Flex>
              </Box>
            ))
          }
          </Flex>
        </Box>

        {/*  markets.tabs  active  */}
        {renderIf(markets.tabs.length > 0)(
          <TabNav ref={el => this.tabNav = el}
            tabs={markets.tabs.map(t => ({
              // text: t,
              text: intl.formatMessage({id: t}),
              value:t
            }))}
            onChange={selected => {
              let tab = _.find(markets.tabs, t => t === selected.value)
              setCurrentTab(tab)
            }}
          />
        )}

        <Box px="mainPadding">
          <Flex height=".4rem" alignItems="center" justifyContent="space-between" color="c2" lineHeight="s1">
            <Box width="1rem">{intl.formatMessage({id: 'exchangePair'})}</Box>
            <Box width="1.2rem" textAlign="right">{intl.formatMessage({id: 'pair.list.header.latest'})}</Box>
            <Box width="1.2rem" textAlign="right">{intl.formatMessage({id: 'pair.list.header.variation'})}</Box>
          </Flex>
          {
            _.map(pairs, record => {
              let { market, close, variation } = record
              let [baseSymbol, quoteSymbol] = market.split('-')
              let haveStar = _.find(markets.stars, {market: record.market})

              return (
                <Flex key={market} height=".4rem" alignItems="center" justifyContent="space-between" sx={{
                    borderTop: '1px solid',
                    borderTopColor: 'border',
                  }}
                  onClick={event => {
                    setCurrentMarket(record)
                    updateAppState({showMarketDrawer: false})
                    // 
                    history.push(`/${locale}/trade/${record.market}`)
                  }}
                >
                  <Flex width="1rem">
                    <Box mr="s2" onClick={event => {
                      event.stopPropagation()
                      starMarket(record)
                    }}>
                      <Svg color={haveStar ? '#0078FF' : '#c8c8c8'} icon="icon-Starlargegray" size=".12rem"></Svg>
                    </Box>
                    <Text>{baseSymbol}</Text>
                    <Text color="c2">/{quoteSymbol}</Text>
                  </Flex>
                  <Box width="1.2rem" textAlign="right">{close}</Box>
                  <Box width="1.2rem" textAlign="right" color={variation >= 0 ? "c3" : "c4"}>{numeral(variation).format('+0.00%')}</Box>
                </Flex>
              )
            })
          }
        </Box>
      </Page>
    )
  }
}

const mapStateToProps = ({markets, persistData}) => {
  return { markets, persistData };
};

const mapDispatchToProps = ({markets, appState}) => {
  return {
    setCurrentTab: markets.setCurrentTab,
    setCurrentMarket: markets.setCurrentMarket,
    starMarket: markets.starMarket,
    updateFilterCond: markets.updateFilterCond,
    addToHistory: markets.addToHistory,
    clearHistory: markets.clearHistory,
    updateAppState: appState.update,
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Pair))