import _ from 'lodash'
import numeral from 'numeral';
import React from 'react';
import { connect } from 'react-redux';
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { ActionSheet, List, WhiteSpace } from 'antd-mobile'
import { Svg, Spin } from 'components'
import { DepthBg } from 'widgets/Common'
import utils from 'utils'

const depthTypes = [
  { key: 'all', text: '' },
  { key: 'sell', text: '' },
  { key: 'buy', text: '' },
]

const lineHeight = .19
const maxLines = 14
@injectIntl
class Depth extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeType: depthTypes[0]
    }
  }

  componentDidMount() {
  }
  
  componentWillUnmount() {
  }

  renderPrecisions () {
    let precisions = this.props.precisions()
    return (
      <Menu onClick={({key}) => {
        let { markets, setLevel } = this.props
        let precision = Number(key)
        let level = markets.currentMarket.pricePrecision - precision
        setLevel(level)
      }}>
      {
        precisions.map((precision) => (
          <Menu.Item key={precision}>
            <Text fontSize={[0, 0, 0]} color='hint'>{ precision }<FormattedMessage id="depth.operations.level"/></Text>
          </Menu.Item>
        ))
      }
      </Menu>
    )
  }

  onSelectType () {
    let { intl } = this.props
    let options = depthTypes.map(t => t.text)
    ActionSheet.showActionSheetWithOptions(
      {
        options: [...options, intl.formatMessage({id: "cancel"})],
        cancelButtonIndex: options.length,
        maskClosable: true,
      },
      index => {
        let activeType = depthTypes[index]
        if (!activeType) return
        this.setState({ activeType })
      }
    )
  }

  onSelectPrecision () {
    let { markets, setLevel, intl } = this.props
    let precisions = this.props.precisions()
    let options = precisions.map(t => `${t}${intl.formatMessage({id: "depth.operations.level"})}`)
    ActionSheet.showActionSheetWithOptions(
      {
        options: [...options, intl.formatMessage({id: "cancel"})],
        cancelButtonIndex: options.length,
        maskClosable: true,
      },
      index => {
        let precision = precisions[index]
        if (!precision) return
        let level = markets.currentMarket.pricePrecision - precision
        setLevel(level)
      }
    )
  }

  onSetPrice (price) {
    this.props.updatePlaceOrder({
      buyPrice: price,
      buyAmount: null,
      buyTotal: null,
      marketBuyTotal: null,
    })
    this.props.updatePlaceOrder({
      sellPrice: price,
      sellAmount: null,
      sellTotal: null,
      marketSellAmount: null,
    })
  }

  render() {
    let { markets, depth, trade: { latestPrice, variation }, currentPrecision, intl } = this.props
    let { activeType } = this.state

    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let depthLines = activeType.key === 'all' ? maxLines / 2 : maxLines
    let {asks, bids} = depth
    asks = _.takeRight(asks, depthLines)
    bids = _.take(bids, depthLines)
    let max = _.maxBy([...asks, ...bids], item => Number(item.size))
    let maxSize = max ? max.size : 100

    return (
      <Box {...this.props}>
        <Flex variant="flexYCenter">
          <Flex color="c1" onClick={() => this.onSelectType()}>
            <Text>{activeType.text}</Text>
            <Svg color="" icon="icon-xiala-copy" size=".16rem"></Svg>
          </Flex>
          <Flex color="c1" onClick={() => this.onSelectPrecision()}>
            <Text>{currentPrecision()}<FormattedMessage id="depth.operations.level"/></Text>
            <Svg color="" icon="icon-xiala-copy" size=".16rem"></Svg>
          </Flex>
        </Flex>

        <Flex color="c2" variant="flexYCenter" lineHeight={`${lineHeight}rem`}>
          <Box>{`${intl.formatMessage({id: 'price'})}(${currentMarket.quoteToken})`}</Box>
          <Box>{`${intl.formatMessage({id: 'size'})}(${currentMarket.baseToken})`}</Box>
        </Flex>

        <Spin spinning={depth.loading}>
          {
            utils.renderIf(activeType.key === 'all' || activeType.key === 'sell')(
              <Flex height={`${(depthLines) * lineHeight}rem`} sx={{
                alignItems: 'flex-end'
              }}>
                <Flex className="depth-sell-table" maxHeight={`${(depthLines) * lineHeight}rem`} sx={{
                  overflowY: 'auto',
                  width: '100%',
                  flexDirection: 'column',
                }}>
                  {asks.map(ask => (
                    <Flex key={ask.price} onClick={() => this.onSetPrice(ask.price)} lineHeight={`${lineHeight}rem`} sx={{
                        position: "relative",
                      }}
                      variant="flexYCenter"
                    >
                      <Text color="c3">{ask.price}</Text>
                      <Text>{ask.size}</Text>
                      <DepthBg bg="c6" percentage={ask.size / maxSize}></DepthBg>
                    </Flex>
                  ))}
                </Flex>
              </Flex>
            )
          }
          <Flex color={variation >= 0 ? 'c4' : 'c3'} alignItems="center" justifyContent="flex-start" sx={{
            borderTop: '1px solid #F0F0F0',
            borderBottom: '1px solid #F0F0F0',
            lineHeight: `${lineHeight * 1.5}rem`
          }}>
            <Text fontSize='f2' fontWeight="semibold">{Number(latestPrice).toFixed(currentMarket.pricePrecision - depth.level)}</Text>
            <Text fontSize='f2' ml="s1">${utils.asLegalMoney(latestPrice, currentMarket.quoteToken, 'USD')}</Text>
          </Flex>
          {
            utils.renderIf(activeType.key === 'all' || activeType.key === 'buy')(
              <Box height={`${(depthLines) * lineHeight}rem`} sx={{
                overflowY: 'auto'
              }}>
                {bids.map(bid => (
                  <Flex key={bid.price} variant="flexYCenter" lineHeight={`${lineHeight}rem`} sx={{
                      position: "relative",
                    }}
                    onClick={() => this.onSetPrice(bid.price)}
                  >
                    <Text color="c4">{bid.price}</Text>
                    <Text>{bid.size}</Text>
                    <DepthBg bg="c7" percentage={bid.size / maxSize}></DepthBg>
                  </Flex>
                ))}
              </Box>
            )
          }
        </Spin>
      </Box>
    )
  }
}

const mapStateToProps = ({ markets, depth, trade, rate }) => {
  return {
    markets,
    depth,
    trade,
    rate,
    precisions: () => {
      let market = markets.currentMarket
      if (!market) return []

      let { pricePrecision, maxLevel } = market
      return _.range(pricePrecision, pricePrecision - maxLevel)
    },
    currentPrecision: () => {
      if (!markets.currentMarket) return 0
      return markets.currentMarket.pricePrecision - depth.level
    }
  };
};

const mapDispatchToProps = ({ depth, placeOrder }) => {
  return {
    setLevel: depth.setLevel,
    scrollSellTable: depth.scrollSellTable,
    updatePlaceOrder: placeOrder.update
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Depth);