import React from 'react';
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'

class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: {}
    }
  }

  componentDidMount() {
    this.setState({
      activeTab: this.props.tabs[0]
    })
  }

  // componentWillReceiveProps (newProps) {
  //   if (this.props.tabs !== newProps.tabs) {
  //     this.setState({
  //       activeTab: newProps.tabs[0]
  //     })
  //   }
  // }

  async setActive (tab) {
    await this.props.onChange(tab)
    this.setState({
      activeTab: tab
    })
  }

  render() {
    let { tabs, ...restProps } = this.props
    let activeTabValue = this.state.activeTab && this.state.activeTab.value
    return (
      <Flex {...restProps} height=".4rem" alignItems='center' justifyContent='space-between' >
        {
          tabs.map(tab => (
            <Flex flex={1} key={tab.text} height="100%" alignItems='center' justifyContent='center'
              sx={{
                borderTop: '1px solid',
                borderTopColor: 'border',
                borderBottom: '1px solid',
                borderBottomColor: tab.value === activeTabValue ? 'c5': 'border',
                cursor: 'pointer'
              }} onClick={() => this.setActive(tab)}>
              <Text fontSize="f1">{tab.text}</Text>
            </Flex>
          ))
        }
        { this.props.children }
      </Flex>
    )
  }
}

const mapStateToProps = state => {
  return {
  };
};

const mapDispatchToProps = dispatch => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
  null,
  {forwardRef: true}
)(Widget);