import _ from 'lodash';
import React, { forwardRef, useEffect, useState, useImperativeHandle, Fragment } from 'react';
import { connect } from 'react-redux';
import { useIntl, FormattedMessage } from 'react-intl';
import { SignInModal, UpsertPasswordModal } from '../Dialog'
import {
  Box,
  Flex,
  Text
} from 'rebass'
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
import { Modal } from 'antd-mobile'
const alert = Modal.alert
import { store } from 'store'
import utils from 'utils';

const Widget = forwardRef(({
  disabled,
  onSuccess = () => {},
  children,
  metaMask,
  dexAccount,
  signIn,
  initAccountFromMetaMask
}, ref) => {
  const intl = useIntl()

  const checkMetaMaskInstalled = () => {
    let valid = metaMask.installed
    if (valid) return true

    return new Promise((resolve, reject) => {
      alert(intl.formatMessage({id: "modal.billPreCheck.metamask.line1"}), intl.formatMessage({id: "modal.billPreCheck.metamask.line2"}), [
        // { text: '', onPress: () => resolve(false)},
        { text: intl.formatMessage({id: "confirm"}), onPress: () => resolve(false)},
      ])
    })
  }

  const checkNetwork = () => {
    let { isDesiredNetwork } = metaMask

    let invalidText = ''

    if (!isDesiredNetwork) {
      // invalidText = 'MetaMask'
      invalidText = intl.formatMessage({id: "modal.billPreCheck.network"})
    }

    if (!invalidText) return true

    return new Promise((resolve, reject) => {
      alert('', invalidText, [
        // { text: '', onPress: () => resolve(false) },
        { text: '', onPress: () => resolve(false) },
      ])
    })
  }

  const checkAddress = () => {
    //  address  hook  address  nullMetaMask
    let rootState = store.getState()
    let { address } = rootState.dexAccount

    let invalidText = ''

    if (!address) {
      invalidText = intl.formatMessage({id: "modal.billPreCheck.address"})
    }

    if (!invalidText) return true

    return new Promise((resolve, reject) => {
      alert('', invalidText, [
        // { text: '', onPress: () => resolve(false) },
        { text: '', onPress: () => resolve(false) },
      ])
    })
  }

  const loadMetamask = async () => {
    //  initAccountFromMetaMask
    //  address 
    // Metamask accountsChanged  networkChanged 
    //  DKEY 
    // let { address } = dexAccount
    // if (!address) {
      // await initAccountFromMetaMask()
    // }

    let { enable } = metaMask
    // enable
    //  dexAccountaddressmetaMask  enablemetamask 
    if (!enable) {
      await initAccountFromMetaMask()
    }
    return true
  }

  const checkSignedIn = async () => {
    // loadMetamask
    //  account dexAccountdexAccount
    let rootState = store.getState()
    let { state } = rootState.dexAccount
    let signined = utils.checkLogined()
    if (signined) return true
    
    if (state === ENUM_ACCOUNT_STATUS.UNCREATED.getValue()) {
      try {
        await UpsertPasswordModal({type: 'create'})
        return true
      } catch (e) {
        return false
      }
    } else if (state === ENUM_ACCOUNT_STATUS.UNSIGNED.getValue()) {
      try {
        await SignInModal()
        return true
      } catch (e) {
        return false
      }
    }
  }

  useImperativeHandle(ref, () => ({
    checkSignedIn,
    check
  }))

  const check = async () => {
    let passed = false
    //  metamask
    passed = await checkMetaMaskInstalled()
    if (!passed) return false

    //  metamask  MetaMask  MetaMask 
    passed = await loadMetamask()
    if (!passed) return false

    // 
    passed = await checkNetwork()
    if (!passed) return false

    // metamask 
    passed = await checkAddress()
    if (!passed) return false

    // 
    passed = await checkSignedIn()
    if (!passed) return false

    return true
  }

  return (
    <div onClick={async () => {
      if (disabled) return

      let isValid = await check()
      if (isValid) {
        onSuccess()
      }
    }}>
      {children}
    </div>
  )
})


const mapStateToProps = ({ metaMask, dexAccount }) => {
  return {
    metaMask,
    dexAccount
  };
};

const mapDispatchToProps = ({ dexAccount, metaMask }) => {
  return {
    initAccountFromMetaMask: metaMask.initAccountFromMetaMask,
    signIn: dexAccount.signIn
  };
};

export default connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true})(Widget)