
import React from 'react'

export default ({
  color,
  icon,
  width,
  height,
  size,
  style,
  ...props
}) => (
  <svg className="icon" aria-hidden="true" style={{
    width: width || size || '.12rem',
    height: height || size || '.12rem',
    ...style
  }} {...props}>
    <use xlinkHref={`#${icon}`} style={{ color }}></use>
  </svg>
)