
import React from 'react'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Svg } from 'components';

export default ({
  width = '.55rem',
  max = Number.POSITIVE_INFINITY,
  min = 0,
  precision,
  value,
  onKeyPress,
  onChange,
  onBlur,
  onFocus
}) => {
  max = Number(max)
  min = Number(min)
  
  let formatValue = (value) => {
    value = _.max([min, value])
    value = _.min([max, value])
    value = _.round(value, precision)
    return value
  }

  let adjust = (steps) => {
    value = Number(value)
    if (!_.isNumber(value)) {
      value = 0
    }
    let step = Math.pow(10, -precision).toFixed(precision)
    let changed = formatValue(value + step * steps)
    onChange(changed)
  }

  return (
    <Flex sx={{
      justifyContent: 'space-between',
      width: '100%',
      height: '.35rem',
      lineHeight: '.35rem',
      border: '1px solid #F0F0F0',
      borderRadius: '3px',
    }}>
      <Box width='.3rem' lineHeight='.35rem' textAlign='center' onClick={() => adjust(-1)}>
        <Svg color="#0078FF" icon="icon-minus" size=".12rem"></Svg>
      </Box>
      <input style={{
          width,
          border: 0,
          textAlign: 'left'
        }}
        type="number"
        pattern="[0-9]*"
        value={value || ''}
        onKeyPress={onKeyPress}
        onChange={e => {
          onChange(e.target.value)
        }}
        onBlur={param => onBlur(param)}
        onFocus={param => onFocus(param)}
      ></input>
      <Box width='.3rem' lineHeight='.35rem' textAlign='center' onClick={() => adjust(1)}>
        <Svg color="#0078FF" icon="icon-plus" size=".12rem"></Svg>
      </Box>
    </Flex>
  )
}