import numeral from 'numeral';
import _ from 'lodash'
import moment from 'moment'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { Svg, Spin } from 'components';
import { List, Picker, Calendar, Drawer } from 'antd-mobile';
import { ENUM_TX_STATUS } from 'utils/enum'
import { config } from 'librarys/lightcone_v3';
import utils, { renderIf } from 'utils';

@injectIntl
class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filters: {
        symbol: undefined,
        side: undefined,
        status: undefined
      },
      showDrawer: false,
      showDataRanger: false
    }
  }

  async componentDidMount() {
    this.props.fetchTransactions()
  }

  async onSymbolChanged (symbol) {
    this.setState(({ filters }) => {
      filters.symbol = symbol
      return { filters }
    })
    await this.props.updateFilterCond({ token: symbol })
  }

  async onSideChanged (side) {
    this.setState(({ filters }) => {
      filters.side = side
      return { filters }
    })

    let types = side && (side === 'WITHDRAW' ? ['onchainWithdrawal'] : ['deposit'])
    await this.props.updateFilterCond({ types })
  }

  async onStatusChanged (status) {
    this.setState(({ filters }) => {
      filters.status = status
      return { filters }
    })
    await this.props.updateFilterCond({ statuses: [status] })
  }

  renderFilters () {
    let { tokens: { list }, intl, transfer, updateFilterCond, fetchTransactions } = this.props
    let { filters: { symbol, side, status } } = this.state
    
    const symbols = list.map(token => token.symbol)
    let { start, end } = transfer.cond
    const sideOptions = [
      { label: intl.formatMessage({id: 'deposit'}), value: 'DEPOSIT' },
      { label: intl.formatMessage({id: 'withdraw'}), value: 'WITHDRAW' },
    ]
    let sideOption = _.find(sideOptions, {value: side})
    return (
      <>
        <List>
          <Picker data={symbols.map(s => ({label: s, value: s}))}
            cols={1}
            // value={symbol}
            onChange={async ([symbol]) => {
              this.onSymbolChanged(symbol)
            }}
          >
            <List.Item arrow="horizontal">
              <FormattedMessage id='coin' />
              {symbol && <List.Item.Brief>{symbol}</List.Item.Brief>}
            </List.Item>
          </Picker>

          <Picker data={sideOptions}
            cols={1}
            // value={side}
            extra=""
            onChange={async ([side]) => {
              this.onSideChanged(side)
            }}
          >
            <List.Item arrow="horizontal" multipleLine >
              <FormattedMessage id='side' />
              {sideOption && <List.Item.Brief>{sideOption.label}</List.Item.Brief>}
            </List.Item>
          </Picker>

          <Picker data={ENUM_TX_STATUS.getOptions()}
            cols={1}
            // value={status}
            onChange={async ([status]) => {
              this.onStatusChanged(status)
            }}
          >
            <List.Item arrow="horizontal">
              <FormattedMessage id='status' />
              {status && <List.Item.Brief>{intl.formatMessage({id: `enum.txStatus.${status}`})}</List.Item.Brief>}
            </List.Item>
          </Picker>

          <List.Item arrow="horizontal"
            onClick={() => this.setState({showDataRanger: true})}
            extra=""
            multipleLine
          >
            <FormattedMessage id='date' />
            {
              renderIf(start)(
                <List.Item.Brief>{`${moment(start).format('MM-DD HH:mm')} ~ ${moment(end).format('MM-DD HH:mm')}`}</List.Item.Brief>
              )
            }
          </List.Item>
          <Calendar
            visible={this.state.showDataRanger}
            onCancel={() => this.setState({showDataRanger: false})}
            onConfirm={async (start, end) => {
              this.setState({showDataRanger: false})
              start = start && moment(start).startOf('day').toDate().valueOf()
              end = end && moment(end).endOf('day').toDate().valueOf()

              console.info({start, end})
              await updateFilterCond({
                start,
                end
              })
            }}
            defaultDate={new Date()}
            pickTime={true}
            showShortcut={true} 
          />
        </List>
        <Box flex={1}></Box>
        <Flex height=".5rem">
          <Flex variant="flexCenter" fontSize="f4" flex={1} bg="border" onClick={() => {
            this.setState(({ filters }) => {
              filters.symbol = undefined
              filters.side = undefined
              filters.status = undefined
              return { filters }
            })

            updateFilterCond({
              symbol: undefined,
              statuses: undefined,
              types: undefined,
              start: undefined,
              end: undefined,
            })
          }}></Flex>
          <Flex variant="flexCenter" fontSize="f4" flex={1} bg="c5" color="white" onClick={() => {
            fetchTransactions()
            this.setState({showDrawer: false})
          }}></Flex>
        </Flex>
      </>
    )
  }

  render() {
    let { transfer, intl } = this.props
    // TODO:  render 
    
    let transfers = transfer.list.map(transfer => {
      let { symbol, amount, realAmount, feeAmount } = transfer
      return {
        ...transfer,
        amount: utils.fromWEI(symbol, amount),
        realAmount: utils.fromWEI(symbol, realAmount),
        feeAmount: utils.fromWEI('ETH', feeAmount)
      }
    })

    return (
      <Drawer
        className="transfer-list-drawer"
        position='left'
        sidebarStyle={{
          width: '80%',
          height: document.documentElement.clientHeight, background: 'white',
          display: 'flex',
          flexDirection: 'column'
        }}
        sidebar={this.renderFilters()}
        open={this.state.showDrawer}
        onOpenChange={open => this.setState({showDrawer: open})}
      >
        <Flex justifyContent="space-between" px="s3" py="s3" fontSize="f3" sx={{
          borderBottom: '1px solid',
          borderBottomColor: 'border',
        }}>
          <Box>{intl.formatMessage({id: 'mine.balance.operations.record'})}</Box>
          <Flex alignItems="center" onClick={() => this.setState({showDrawer: true})}>
            <Svg color="#0078FF" icon="icon-shaixuan" size=".18rem"></Svg>
            <Text ml="s2" color="c5">{intl.formatMessage({id: 'filter'})}</Text>
          </Flex>
        </Flex>

        <Spin spinning={transfer.loading}>
          {transfers.map(transfer => {
            let { timestamp, symbol, realAmount, feeAmount, txType, status } = transfer
            feeAmount = txType === 'deposit' ? feeAmount : -feeAmount
            return (
              <Flex mx="s3" py="s3" key={timestamp} flexDirection="column" sx={{
                borderBottom: '1px solid',
                borderBottomColor: 'border',
              }}>
                <Flex justifyContent="space-between" fontSize="f3">
                  <Text>{symbol}</Text>
                  <Text color={realAmount >= 0 ? 'c4' : 'c3'}>{numeral(realAmount).format('0,0.[00000000]')}</Text>
                </Flex>
                <Flex justifyContent="space-between" mt="s2">
                  <Text>{moment(timestamp).format('YYYY-MM-DD HH:mm:ss')}</Text>
                  <Text>{intl.formatMessage({id: 'fee'})} {numeral(feeAmount).format('0,0.[00000000]')} ETH</Text>
                </Flex>
                <Flex justifyContent="space-between" mt="s2">
                  <Text>{intl.formatMessage({id: txType === 'deposit' ? 'deposit' : 'withdraw'})}</Text>
                  <Text>{intl.formatMessage({id: `enum.txStatus.${status}`})}</Text>
                </Flex>
              </Flex>
            )
          })}
        </Spin>
      </Drawer>
    )
  }
}

const mapStateToProps = ({ transfer, markets, tokens }) => {
  return {
    transfer,
    markets,
    tokens
  };
};

const mapDispatchToProps = ({ transfer }) => {
  return {
    fetchTransactions: transfer.fetchTransactions,
    updateFilterCond: transfer.updateFilterCond,
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget))