const Widget = ({errors, items}) => {
  const intl = useIntl()

  return (
    <Box textAlign="left">
      <Text color="#969696" fontSize={'.12rem'}>
        {intl.formatMessage({id: "modal.errorInfo.label"})}
      </Text>
      {
        _.map(items, item => {
          let error = !!_.includes(errors, item)
          return (
            <Flex key={item} alignItems="center">
              <Text bg={error ? '#F0505A' : '#50BE8C'} sx={{
                width: '.1rem',
                height: '.1rem',
                borderRadius: '50%'
              }}></Text>
              <Text lineHeight={'.2rem'} fontSize={'.12rem'} line color="#969696" ml={'6px'}>{item}</Text>
            </Flex>
          )
        })
      }
    </Box>
  )
}

