import React, { Component, useEffect, useState } from 'react'
import { connect, Provider } from 'react-redux'
import { ConnectedRouter } from 'connected-react-router'
import { IntlProvider } from 'react-intl'
import { messages } from './locales'
import { ThemeProvider } from 'emotion-theming'
import Routes from './router';
import theme from 'theme'
import { ActivityIndicator } from 'antd-mobile';

const RouterWidget = ({
  store, history, ready, loading, locale,
  startup
}) => {
  const renderLoading = () => {
    // cssstyle
    return (
      <ActivityIndicator
        toast
        text=""
        animating={true}
      />
    )
  }

  return (
    <Provider store={store}>
      <ThemeProvider theme={theme}>
        <IntlProvider locale={locale} key={locale} messages={messages[locale]}>
          <ConnectedRouter history={history}>
            {!ready ? renderLoading() : <Routes />}
          </ConnectedRouter>
          <ActivityIndicator
            toast
            text=""
            animating={loading}
          />
        </IntlProvider>
      </ThemeProvider>
    </Provider>
  )
}

const mapStateToProps = ({appState, persistData}) => {
  return {
    ready: appState.ready,
    loading: appState.loading,
    locale: persistData.locale,
  }
}

const mapDispatchToProps = ({appState}) => ({
})

export default connect(mapStateToProps, mapDispatchToProps)(RouterWidget)
