import _ from 'lodash'
import moment from 'moment'
import React from 'react';
import ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { Page, InfiniteScroll } from 'components'
import api from 'api'

@injectIntl
class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      list: [],
      listHeight: document.documentElement.clientHeight - 40
    }
  }

  componentDidMount() {
    // 
    // 
    const listHeight = document.documentElement.clientHeight - ReactDOM.findDOMNode(this.infinite).parentNode.offsetTop
    console.info(ReactDOM.findDOMNode(this.infinite))
    this.setState({listHeight})
  }

  onChangeTab (key) {
    if (key === 'mine') {
    } else {
    }
  }

  fetchData = async function (pageParams) {
    this.setState({ loading: true })

    let { dexAccount } = this.props
    let resp = await api.getUserBillEggPrizeList({
      // address: '0x631562b916ab39d1753a61a5c55dc546528da790',
      address: dexAccount.address,
      ...pageParams,
      sort: 'billTime',
      sortDirection: 'desc',
      // TODO: delete
      startDate: '2020-01-02 18:00:00'
    })
    this.setState({ loading: false })
    return resp
  }.bind(this)
  
  renderItem = function (item) {
    let { id, billTime, winnerWalletAddr, money, unit } = item

    return (
      <Flex key={id} height=".4rem" alignItems="center" justifyContent="space-between" sx={{
        borderTop: '1px solid',
        borderTopColor: 'border',
      }}>
        <Flex width="1.5rem"><Text ml="s1" color="c2">{moment(billTime).format('YYYY-MM-DD HH:mm:ss')}</Text></Flex>
        {/* <Box width="1.1rem" textAlign="right">{winnerWalletAddr}</Box> */}
        <Box width="0.5rem" textAlign="center">{money}</Box>
        <Box width="0.5rem" textAlign="center">{unit}</Box>
      </Flex>
      
    )
  }.bind(this)

  render() {
    let { intl } = this.props
    
    return (
      <Page>
        <Box px="s2" height="100%" flexDirection="column">
          <Flex height=".4rem" alignItems="center" justifyContent="space-between" color="c2" lineHeight="s1">
            <Box width="1.5rem">{intl.formatMessage({id: 'timestamp'})}</Box>
            {/* <Box width="1.1rem" textAlign="right"></Box> */}
            <Box width="0.5rem" textAlign="center">{intl.formatMessage({id: 'mine.award.egg.list.header.amount'})}</Box>
            <Box width="0.5rem" textAlign="center">{intl.formatMessage({id: 'tokenName'})}</Box>
          </Flex>
          <Flex height={this.state.listHeight}>
            <InfiniteScroll ref={ref => this.infinite = ref}
              listKey="rows"
              autoHeight={true}
              // height={this.state.listHeight}
              // height={410}
              renderItem={this.renderItem} 
              fetchData={this.fetchData}
              // parseRetList={ret => ret.data.data.dataList}
              enableRefresh={true}
              onRefresh={async () => {
                await this.infinite.reset()
                // await this.refreshDatas()
              }}
              // renderEmptyList={() => (
              //   <div style={{ padding: 0, textAlign: 'center' }}></div>
              // )}
              // initialListSize={20}
              // useBodyScroll={true}
            ></InfiniteScroll>
          </Flex>
        </Box>
      </Page>
    )
  }
}

const mapStateToProps = ({ dexAccount, balance, walletBalance }) => {
  return {
    dexAccount,
    balance,
    walletBalance
  };
};

const mapDispatchToProps = ({ balance, walletBalance, transfer }) => {
  return {
    fetchBalances: balance.fetchBalances,
    fetchWalletBalances: walletBalance.fetchAllBalances,
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget))