import _ from 'lodash'
import moment from 'moment'
import React from 'react';
import { connect } from 'react-redux';
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { ActionSheet, Switch, WhiteSpace } from 'antd-mobile'
import utils, { renderIf, Toast } from 'utils';
import { TabNav } from './Common'
import { Svg, Spin } from 'components'

@injectIntl
class MyOrder extends React.Component {
  constructor(props) {
    super(props);
    this.state = {}
  }

  componentDidMount() {
  }

  onTabChange (tab) {
    this.props.updateFilterCond({
      type: tab.value,
      page: 1,
      pageSize: 50
    })
    this.props.fetchOrders()
  }

  onChangeShowAll (checked) {
    this.props.updateFilterCond({
      allMarket: checked,
      page: 1,
      pageSize: 50
    })
    this.props.fetchOrders()
  }

  renderOrder = function (row) {
    let { intl, order, cancelAnOrders, fetchOrders } = this.props
    
    let { createdAt, hash, market, side, price, size, filledVolume, filledSize, filled, status } = row
    let [baseSymbol, quoteSymbol] = market.split('-')
    return (
      <Box key={hash} mx="mainPadding" py=".06rem" sx={{
        borderTop: '1px solid',
        borderTopColor: 'border',
      }}>
        <Flex justifyContent="space-between">
          <Box>{moment(createdAt).format('MM-DD HH:mm:ss')}</Box>
          {
            renderIf(order.cond.type === 'current')(
              <Box color={row.filled === '100%' ? 'c2' : 'c5'} onClick={async () => {
                if (row.filled === '100%') return
                await cancelAnOrders(row)
                Toast.success(intl.formatMessage({id: 'myOrder.notify.cancelSuccess'}))
                fetchOrders()
              }}>
                <FormattedMessage id="myOrder.operations.cancel" />
              </Box>
            )
          }
        </Flex>
        <Flex justifyContent="space-between" color="c2" lineHeight="s1">
          <Box width=".5rem">{intl.formatMessage({id: 'exchangePair'})}</Box>
          <Box width=".25rem">{intl.formatMessage({id: 'side'})}</Box>
          <Box width=".7rem" textAlign="right">{intl.formatMessage({id: 'price'})}</Box>
          <Box width=".7rem" textAlign="right">{intl.formatMessage({id: 'amount'})}</Box>
          <Box width=".4rem" textAlign="right">{intl.formatMessage({id: 'mine.order.list.header.volume'})}</Box>
        </Flex>
        <Flex justifyContent="space-between" lineHeight="s1">
          <Flex width=".5rem">{baseSymbol}<Text color="c2">/{quoteSymbol}</Text></Flex>
          <Text width=".25rem" color={side === 'BUY' ? "c4" : "c3"}>{intl.formatMessage({id: _.toLower(side)})}</Text>
          <Box width=".7rem" textAlign="right">{price}</Box>
          <Box width=".7rem" textAlign="right">{size}</Box>
          <Box width=".4rem" textAlign="right">{filled}</Box>
        </Flex>
      </Box>
    )
  }.bind(this)

  render() {
    let { intl, order, cancelAllOrders } = this.props

    const tabs = [
      { text: intl.formatMessage({id: 'myOrder.tabs.current'}), value: 'current' },
      { text: intl.formatMessage({id: 'myOrder.tabs.history'}), value: 'history' },
    ]

    const filters = [
      { text: intl.formatMessage({id: 'myOrder.filters.all'}), value: 'ALL' },
      { text: intl.formatMessage({id: 'myOrder.filters.processed'}), value: 'PROCESSED' },
      { text: intl.formatMessage({id: 'myOrder.filters.cancelled'}), value: 'CANCELLED' },
    ]

    return (
      <Flex {...this.props} flexDirection="column">
        <TabNav tabs={tabs} onChange={tab => this.onTabChange(tab)} />
        <Flex variant="flexYCenter" px="mainPadding" height=".4rem">
          <Box sx={{
            transform: `scalex(${0.7 * window.ratioToIphone6}) scaley(${0.7 * window.ratioToIphone6})`,
            width: 'fit-content'
          }}>
            <Switch platform="ios" size="small" checked={order.cond.allMarket} color="#0078FF" onChange={checked => this.onChangeShowAll(checked)}></Switch>
          </Box>
          <Text color="c2" fontWeight="medium" flex={1}>{intl.formatMessage({id: 'myOrder.operations.showAll'})}</Text>
          {
            renderIf(order.cond.type === 'current')(
              <Text color="c5" onClick={() => cancelAllOrders()}
                sx={{ cursor: 'pointer' }}
              >
                {intl.formatMessage({id: 'myOrder.operations.cancelLatest'})}
              </Text>
            )
          }
          {
            renderIf(order.cond.type === 'history')(
              <Flex color="c1" onClick={() => {
                let options = filters.map(t => t.text)
                ActionSheet.showActionSheetWithOptions(
                  {
                    options: [...options, intl.formatMessage({id: 'cancel'})],
                    cancelButtonIndex: options.length,
                    maskClosable: true,
                  },
                  index => {
                    let condType = filters[index]
                    if (!condType) return

                    let historyStatus = condType.value
                    this.props.updateFilterCond({
                      historyStatus,
                      page: 1,
                      pageSize: 50
                    })
                    this.props.fetchOrders()
                  }
                )
              }}>
                <Text>{_.find(filters, {value: order.cond.historyStatus}).text}</Text>
                <Svg color="" icon="icon-xiala-copy" size=".16rem"></Svg>
              </Flex>
            )
          }
        </Flex>
        {/* <InfiniteScroll ref={ref => this.infinite = ref}
          autoHeight={true}
          renderItem={this.renderOrder} 
          fetchData={this.fetchOrders}
          // parseRetList={ret => ret.data.data.dataList}
          // renderEmptyList={() => (
          //   <div style={{ padding: 0, textAlign: 'center' }}></div>
          // )}
        ></InfiniteScroll> */}
        <Flex flexDirection="column">
          <Spin spinning={order.loading}>
            {
              _.map(order.list, order => this.renderOrder(order))
            }
          </Spin>
        </Flex>
      </Flex>
    )
  }
}

const mapStateToProps = ({ order }) => {
  return {
    order,
  };
};

const mapDispatchToProps = ({ order }) => {
  return {
    updateFilterCond: order.updateFilterCond,
    fetchOrders: order.fetchOrders,
    cancelAnOrders: order.cancelAnOrders,
    cancelAllOrders: () => utils.withLoading(() => order.cancelAllOrders())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MyOrder);