import _ from 'lodash'
import moment from 'moment'
import React, { forwardRef, useEffect, useState, useImperativeHandle, useRef } from 'react'
import { connect } from 'react-redux'
import { withRouter } from "react-router-dom"
import { injectIntl, useIntl } from 'react-intl';
import {
  Box,
  Flex,
  Text,
  Image
} from 'rebass'
import { ActionSheet, Toast, Slider, List } from 'antd-mobile'
import utils, { renderIf } from 'utils'
import { config, common } from 'librarys/lightcone_v3';
import { Page, Svg } from 'components'
import { Input, DepthHorizontal, TVChart, TabNav } from 'widgets'
import api from 'api'
import switchSideIcon from 'icons/switchSide.png'

const gasLimitInWEI = Number(config.getGasLimitByType("create").gasInWEI)
const withdrawFeeInWEI = config.getFeeByType('withdraw').feeInWEI

const Widget = ({
  onDestory,
  balance: balanceStore,
  walletBalance: walletBalanceStore,
  rate,
  persistData,
  fetchBalances,
  fetchWalletBalances,
  transfer,
  history
}) => {
  const intl = useIntl()
  let { initialSymbol, initialType = 'deposit' } = history.location.data || {}
  const [symbol, setSymbol] = useState(initialSymbol)
  const [type, setType] = useState(initialType)
  const [transferAmount, setTransferAmount] = useState(0)
  const [recommendedGasEth, setRecommendedGasEth] = useState([0, 0, 0])
  const [gasEth, setGasEth] = useState(0)

  useEffect(() => {
    // fetchBalances()
    fetchWalletBalances()

    api.getRecommendedGasPrice().then(resp => {
      let [min, normal, max] = resp
      
      let minGas = utils.fromWEI('ETH', min * gasLimitInWEI)
      let normalGas = utils.fromWEI('ETH', normal * gasLimitInWEI)
      let maxGas = utils.fromWEI('ETH', max * gasLimitInWEI)

      setRecommendedGasEth([Number(minGas), Number(normalGas), Number(maxGas)])
      setGasEth(normalGas)
    })
  }, [])

  const getAvailable = () => {
    let available = 0
    if (symbol) {
      if (type === 'deposit') {
        let balance = _.find(walletBalanceStore.list, b => b.symbol === symbol)
        available = balance ? balance.amount : 0
      } else {
        let balance = _.find(balanceStore.list, b => b.token.symbol === symbol)
        available = balance ? balance.availAmount : 0
      }
      available = Number(available)
      available = _.floor(available, 6)
    }
    return available
  }

  const getWalletEthAvail = () => {
    let ethBalance = _.find(walletBalanceStore.list, b => b.symbol === 'ETH')
    return ethBalance ? ethBalance.amount : 0
  }

  let balances = balanceStore.list
  let balancesSymbols = balances.map(b => b.token.symbol)
  let walletBalances = walletBalanceStore.list
  let walletBalancesSymbols = walletBalances.map(b => b.symbol)
  let symbolOptions = _.union(balancesSymbols, walletBalancesSymbols)

  //  symbol symbolOptions
  console.info({symbol})
  if (!symbol) {
    if (symbolOptions[0]) {
      console.info(' symbol', symbolOptions[0])
      setSymbol(symbolOptions[0])
    }
    return <></>
  }

  // getFeeByType
  // 
  // 
  // ETH 0
  let walletEthAvail = getWalletEthAvail()
  let withdrawFee = utils.fromWEI('ETH', withdrawFeeInWEI)
  
  // 
  let available = getAvailable()
  //  ETH 
  if (symbol === 'ETH' && type === 'deposit') {
    let ethAvailable = _.floor(available - gasEth, 6)
    available = _.max([ethAvailable, 0])
  }

  const submit = async () => {
    let { lastTransferTime } = persistData
    let duaration = moment().diff(moment(lastTransferTime), 'seconds')
    if (duaration < 30) {
      Toast.info(intl.formatMessage({ id: `notify.transfer.duaration`}, { second: 30 - duaration }))
      return
    }

    if (type === 'withdraw') {
      if (walletEthAvail < withdrawFee) {
        return
      }
    } else if (type === 'deposit') {
      if (gasEth >= walletEthAvail) {
        return
      }
    }
    if (transferAmount <= 0) return

    if (transferAmount > available) {
      Toast.info('')
      return
    }

    console.info({type, symbol, transferAmount, gasEth})
    let result = await transfer(type, symbol, transferAmount, gasEth)
    if (result) {
      // Toast.info('')
      history.goBack(-1)
    }
  }

  console.info('TransferCreate render')
  return (
    <Page fontSize="f2">
      <Flex flexDirection="column" alignItems="center" padding="s4" position="relative">
        <Input label={intl.formatMessage({id: 'modal.transfer.label.coin'})} value={symbol} type="select" onClick={() => {
          let options = symbolOptions
          ActionSheet.showActionSheetWithOptions(
            {
              options: [...options, intl.formatMessage({id: 'cancel'})],
              cancelButtonIndex: options.length,
              maskClosable: true,
            },
            index => {
              let symbol = options[index]
              if (!symbol) return
              setSymbol(symbol)
              setTransferAmount(0)
            }
          )
        }} />

        <Input label={intl.formatMessage({id: 'type'})} value={intl.formatMessage({id: type})} type="select" onClick={() => {
          ActionSheet.showActionSheetWithOptions(
            {
              options: [
                intl.formatMessage({id: 'deposit'}),
                intl.formatMessage({id: 'withdraw'}),
                intl.formatMessage({id: 'cancel'})
              ],
              cancelButtonIndex: 2,
              maskClosable: true,
            },
            index => {
              let options = [ 'deposit', 'withdraw' ]
              let type = options[index]
              if (!type) return
              console.info(type)
              setType(type)
              setTransferAmount(0)
            }
          )
        }} />

        {/*  stepper  inpititem ui  */}
        <Input label={intl.formatMessage({id: 'amount'})} disabled={false} value={transferAmount} onChange={value => setTransferAmount(value)} />

        <Flex mt="s3" width="100%" justifyContent="space-between">
          <Text color="c2">{intl.formatMessage({id: 'modal.transfer.label.available'})} {available}</Text>
          <Text  color="c5"
            onClick={() => setTransferAmount(available)}
          >
            {intl.formatMessage({id: 'modal.transfer.all'})}
          </Text>
        </Flex>
        {
          renderIf(available - transferAmount < 0)(
            <Text mt="s3" color="#F0505A">{intl.formatMessage({id: 'modal.transfer.hint.notAvailable'})}</Text>
          )
        }
        {/* {
          renderIf(type === 'deposit')(
            <>
              <Input label="" value={`${gasEth} ETH $${utils.asLegalMoney(gasEth, 'ETH', 'USD')}`} />

              {renderIf(gasEth >= walletEthAvail)(
                <Text mt="s3" color="#F0505A">{` ${walletEthAvail} ETH`}</Text>
              )}
              <Box mt="s4" height="50px" width="100%">
                <List.Item align="top">
                  <Box px="12px" pt="10px" height="50px">
                    <Slider size="small" prefixCls="gas-slider am-slider"
                      handleStyle={{
                        height: '18px',
                        width: '18px',
                        marginLeft: '-9px',
                        marginTop: '-7px',
                      }}
                      min={recommendedGasEth[0]} max={recommendedGasEth[2]} marks={{
                        [recommendedGasEth[0]]: '',
                        // [recommendedGasEth[1]]: '',
                        [recommendedGasEth[2]]: '',
                      }} step={0.000001} value={gasEth} onChange={value => setGasEth(value)} />
                  </Box>
                </List.Item>
              </Box>
            </>
          )
        } */}
        {/* {
          renderIf(type === 'withdraw')(
            <Box>
              <Input label="" value={'0.002ETH'} />
              {
                renderIf(walletEthAvail < withdrawFee)(
                  <Text mt="s3" color="#F0505A">{` ${withdrawFee} ETH  ${walletEthAvail}ETH `}</Text>
                )
              }
              {
                renderIf(walletEthAvail >= withdrawFee)(
                  <Text mt="s3" color="#F0505A">{`GASETH`}</Text>
                )
              }
            </Box>
          )
        } */}
        <Text mt="s4" bg="c5" color="white" fontSize="f3" fontWeight="medium" sx={{
            borderRadius: '3px',
            width: "100%",
            height: ".46rem",
            lineHeight: "lh7",
            textAlign: 'center'
          }}
          onClick={() => submit()}
        >{intl.formatMessage({id: 'confirm'})}</Text>
        {
          renderIf(type === 'deposit')(
            <Box>
              <Text mt={30} color={"#969696"} fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.deposit.minerFee.hint1'})}</Text>
              <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.deposit.minerFee.hint2'})}</Text>
              <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.deposit.minerFee.hint3'})}</Text>
            </Box>
          )
        }
        {
          renderIf(type === 'withdraw')(
            <Box>
              <Text mt={30} color={"#969696"} fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.withdraw.minerFee.hint1'})}</Text>
              <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.withdraw.minerFee.hint2'})}</Text>
              <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.withdraw.minerFee.hint3'})}</Text>
              <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.withdraw.minerFee.hint4'})}</Text>
              <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.withdraw.minerFee.hint5'})}</Text>
            </Box>
          )
        }
      </Flex>
    </Page>
  )
}

const mapStateToProps = ({ balance, walletBalance, rate, persistData }) => {
  return {
    balance,
    walletBalance,
    rate,
    persistData
  };
};

const mapDispatchToProps = ({ balance, transfer, walletBalance }) => {
  return {
    fetchBalances: balance.fetchBalances,
    fetchWalletBalances: walletBalance.fetchAllBalances,
    transfer: (type, symbol, amount, gasEth) => {
      let gasPriceInWEI = config.toWEI('ETH', gasEth) / gasLimitInWEI
      let gasPrice = common.formatter.toGWEI(gasPriceInWEI)
      return utils.withLoading(() => transfer[type]({symbol, amount, gasPrice}))
    }
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true})(Widget))