import styled from '@emotion/styled'
import numeral from 'numeral';
import {
  Flex,
  Box,
} from 'rebass'
const DepthBg = styled(Box)(
  {
    position: "absolute",
    opacity: 0.15,
  },
  ({percentage, gap, align = 'right'}) => ({
    width: numeral(percentage).format('0%'),
    top: gap ? '1px' : 0,
    bottom: gap ? '1px' : 0,
    left: (align === 'left') && '1px',
    right: (align === 'right') && '1px',
  })
)
export default DepthBg;