import React from 'react';
import { connect } from 'react-redux';
import numeral from 'numeral'
import { injectIntl, FormattedMessage  } from 'react-intl'
import { ActionSheet } from 'antd-mobile'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import styled from '@emotion/styled'
import { TVChartContainer } from './TVChartContainer';
// import { DepthChartContainer } from './DepthChartContainer';
import { Page, Svg } from 'components'
import { CurrentPair } from 'widgets'
import utils, { renderIf } from 'utils'
import api from 'api'
import { ENUM_INTERVAL_TYPE } from 'utils/enum'

const units = ['USD', 'CNY']
const views = {
  DEPTH_VIEW: 0,
  TRADING_VIEW: 1
}
const tvLocalMapping = {
  cn: 'zh',
  us: 'en',
  kr: 'ko',
  ru: 'ru',
  jp: 'ja',
}

// https://emotion.sh/docs/styled#object-styles
const ViewTypeButton = styled(Text)(
  {
    border: '1px solid',
    cursor: 'pointer',
  },
  props => ({
    background: `${props.active ? '#1890ff' : ''}`,
    color: `${props.active ? 'white' : '#1890ff'}`,
    borderColor: `${props.active ? '#1890ff' : '#1890ff'}`,
    borderTopLeftRadius: `${props.left ? 2 : 0}px`,
    borderBottomLeftRadius: `${props.left ? 2 : 0}px`,
    borderTopRightRadius: `${props.right ? 2 : 0}px`,
    borderBottomRightRadius: `${props.right ? 2 : 0}px`,
    borderLeftWidth: `${props.right ? 0 : 1}px`,
    borderRightWidth: `${props.left ? 0 : 1}px`,
  })
)

const IntervalButton = styled(Text)(
  {
    width: '44px',
    height: '18px',
    marginRight: '3px',
    fontSize: '12px',
    lineHeight: '18px',
    background: 'rgba(255,255,255,0.08)',
    cursor: 'pointer',
    borderRadius: '1px',
    textAlign: 'center'
  },
  props => ({
    color: `${props.active ? '#0064FF' : '#78788C'}`,
  })
)

@injectIntl
class TVChart extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      unit: 'USD',
      pairVisible: false,
      tokenInfoVisible: false,
      tokenInfo: {},
      selectedView: views.TRADING_VIEW,

      lineType: 'Candle',
      interval: ENUM_INTERVAL_TYPE.HOUR_1.getValue(),
      fullScreen: false,
      showIndicators: false,
      showSetting: false,
    }
  }

  componentDidMount() {
  }

  renderTvOps () {
    let { selectedView, fullScreen, showIndicators, showSetting, interval } = this.state
    const { intl } = this.props

    let invetvalMapping = [
      // { label: '1', value: ENUM_INTERVAL_TYPE.MIN_1, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.5min'}), value: ENUM_INTERVAL_TYPE.MIN_5, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.15min'}), value: ENUM_INTERVAL_TYPE.MIN_15, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.30min'}), value: ENUM_INTERVAL_TYPE.MIN_30, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.1hr'}), value: ENUM_INTERVAL_TYPE.HOUR_1, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.2hr'}), value: ENUM_INTERVAL_TYPE.HOUR_2, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.4hr'}), value: ENUM_INTERVAL_TYPE.HOUR_4, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.12hr'}), value: ENUM_INTERVAL_TYPE.HOUR_12, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.1d'}), value: ENUM_INTERVAL_TYPE.DAY_1, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.1w'}), value: ENUM_INTERVAL_TYPE.WEEK_1, },
    ]
    let selected = _.find(invetvalMapping, item => item.value.getValue() === interval)
    return (
      <Flex height={30} px={12} bg='border' color='c1' alignItems="center" justifyContent="space-between">
        <Box mr={2} lineHeight={'12px'} className="tvchart-ops with-corner"
          onClick={() => {
            let options = invetvalMapping.map(item => item.label)
            ActionSheet.showActionSheetWithOptions(
              {
                options: [...options, intl.formatMessage({id: 'cancel'})],
                cancelButtonIndex: options.length,
                maskClosable: true,
              },
              index => {
                let item = invetvalMapping[index]
                if (!item) return
                const interval = item.value.getValue()
                this.setState({
                  interval,
                  lineType: 'Candle'
                }, () => {
                  // if (!window.localStorage) return;
                  // window.localStorage.setItem('bn.lineType', type);
                  // window.localStorage.setItem('bn.interval', interval);
                  // window.localStorage.setItem('bn.intervalName', intervalName);
                });
              }
            )
          }}
        >
          {selected.label}
        </Box>
        <Box mr={2} lineHeight={'12px'} className="tvchart-ops with-corner"
          onClick={() => this.setState({ showIndicators: !showIndicators })}
        >
          <FormattedMessage id='indicator' />
        </Box>
        <Box mr={2} lineHeight={'12px'} className="tvchart-ops with-corner"
          onClick={() => this.setState({ showSetting: !showSetting })}
        >
          <FormattedMessage id='setting' />
        </Box>
        <Box mr={2} lineHeight={'12px'} className="tvchart-ops"
          onClick={() => this.setState({ fullScreen: !fullScreen })}
        >
          <Svg color="" icon="iconquanping" size=".12rem"></Svg>
        </Box>
      </Flex>
    )
  }

  render() {
    let { markets: { currentMarket }, ticker: { dayInfo }, persistData } = this.props
    if (!currentMarket) return <></>

    let { baseToken, quoteToken, market, pricePrecision } = currentMarket
    let { high, low, volume, differ, close, variation } = dayInfo

    return (
      <Box {...this.props}>
        <Flex justifyContent="space-between" padding="mainPadding" sx={{
          borderBottom: '1px solid',
          borderBottomColor: 'border',
        }}>
          <Box>
            <CurrentPair />
            <Flex mt={'s1'} color={variation >= 0 ? 'c4' : 'c3'} alignItems="center" fontWeight="medium">
              <Text fontSize={14}>{close.toFixed(pricePrecision)}</Text>
              <Svg icon={variation >= 0 ? 'icon-arrow-up' : 'icon-arrow-down'} size=".2rem"></Svg>
              <Text ml={'s2'} color="c2">$ {utils.asLegalMoney(close, quoteToken, this.state.unit)}</Text>
            </Flex>
            <Flex mt={'s1'}>
              <Text minWidth=".4rem" px="s1" textAlign='center' bg={variation >= 0 ? 'c4' : 'c3'} color="white" sx={{borderRadius: ".02rem"}}>{differ.toFixed(pricePrecision)}</Text>
              {/* <Text ml={'s1'} color={variation >= 0 ? 'c4' : 'c3'}>{numeral(variation).format('+0.00%')}</Text> */}
            </Flex>
          </Box>
          <Box>
            <Flex>
              <Text color='c2' mt={'3px'} mr={'5px'}><FormattedMessage id='tvchar.baseInfo.high' /></Text>
              <Text mt={'2px'}>{high}</Text>
            </Flex>
            <Flex>
              <Text color='c2' mt={'3px'} mr={'5px'}><FormattedMessage id='tvchar.baseInfo.low' /></Text>
              <Text mt={'2px'}>{low}</Text>
            </Flex>
            <Flex>
              <Text color='c2' mt={'3px'} mr={'5px'}><FormattedMessage id='tvchar.baseInfo.volumn' /></Text>
              <Text mt={'2px'}>{volume} {quoteToken}</Text>
            </Flex>
          </Box>
        </Flex>
        { this.renderTvOps() }
        <TVChartContainer style={{height: `3.4rem`}}
          locale={tvLocalMapping[persistData.locale]}
          symbol={market}
          lineType={this.state.lineType}
          interval={this.state.interval}
          showIndicatorsDialog={this.state.showIndicators}
          showSettingDialog={this.state.showSetting}
          visible={this.state.selectedView === views.TRADING_VIEW}
          fullScreen={this.state.fullScreen}
        />
      </Box>
    )
  }
}

const mapStateToProps = ({ markets, trade, ticker, persistData }) => {
  return {
    markets,
    trade,
    ticker,
    persistData
  };
};

const mapDispatchToProps = dispatch => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TVChart);