import _ from 'lodash'
import React, { forwardRef, useEffect, useState, useImperativeHandle, useRef, Fragment } from 'react';
import { useHistory } from "react-router-dom"
import { connect } from 'react-redux'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Button,
  Text
} from 'rebass'
import { TabBar, Toast } from 'antd-mobile'
import { Page, Svg } from 'components'
import { BillPreCheck, EggAward } from 'widgets'
import Exchange from './Exchange'
import Finder from './Finder'
import Balance from './Balance'
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'

const tabs = [
  { key: 'market', title: '', icon: 'icon-bitebi', component: <Exchange/> },
  // { key: 'finder', title: '', icon: 'icon-faxian', component: <Finder/> },
  { key: 'balance', title: '', icon: 'icon-qianbao', component: <Balance/> },
]

const Widget = ({
  dexAccount,
  selectedTab,
  setSelectedTab
}) => {
  const billPreCheck = useRef(null)

  return (
    <Page className="home-page">
      <TabBar
        tintColor="#0078FF"
        unselectedTintColor='#9696A0'
        barTintColor="white"
        prerenderingSiblingsNumber={0}
        prefixCls="am-tab-bar"
      >
        {tabs.map(tab => (
          <TabBar.Item
            key={tab.key}
            title={tab.title}
            icon={
              <Svg color="#9696A0" icon={tab.icon} size=".25rem"></Svg>
            }
            selectedIcon={
              <Svg color="#0078FF" icon={tab.icon} size=".25rem"></Svg>
            }
            onPress={async () => {
              if (tab.key === 'balance' && dexAccount.state !== ENUM_ACCOUNT_STATUS.LOGGED_IN.getValue()) {
                let success = await billPreCheck.current.check()
                if (!success) return
              }
              setSelectedTab(tab.key)
            }}
            selected={selectedTab === tab.key}
          >
            { tab.component }
          </TabBar.Item>
        ))}
      </TabBar>
      <BillPreCheck ref={billPreCheck}></BillPreCheck>
      <EggAward />
    </Page>
  )
}

const mapStateToProps = ({ dexAccount, appState }) => {
  return {
    dexAccount,
    selectedTab: appState.selectedTab
  }
}

const mapDispatchToProps = ({ appState }) => {
  return {
    setSelectedTab: tab => appState.update({selectedTab: tab}),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Widget)