import moment from 'moment';
import React from 'react';
import { connect } from 'react-redux';
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Spin } from 'components'

@injectIntl
class Trade extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
  }
  
  componentWillUnmount() {
  }

  render() {
    let { trade, markets, intl } = this.props

    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    return (
      <Box px="mainPadding">
        <Flex height=".3rem" alignItems="center" justifyContent="space-between" color="c2" lineHeight="s1">
          <Box width="1.15rem">{intl.formatMessage({id: 'timestamp'})}</Box>
          <Box width=".3rem">{intl.formatMessage({id: 'side'})}</Box>
          <Box width="1.15rem" textAlign="right">{intl.formatMessage({id: 'price'})}({currentMarket.quoteToken})</Box>
          <Box width="1.15rem" textAlign="right">{intl.formatMessage({id: 'size'})}({currentMarket.baseToken})</Box>
        </Flex>
        <Spin spinning={trade.loading}>
          {
            _.map(trade.list, item => {
              let { index, timestamp, side, price, size } = item
              return (
                <Flex key={index} height=".3rem" alignItems="center" justifyContent="space-between">
                  <Box width="1.15rem">{moment(timestamp).format('MM-DD HH:mm')}</Box>
                  <Box width=".3rem">{intl.formatMessage({id: side.toLowerCase()})}</Box>
                  <Box width="1.15rem" textAlign="right" color={item.side === 'BUY' ? "c3" : "c4"}>{price}</Box>
                  <Box width="1.15rem" textAlign="right">{size}</Box>
                </Flex>
              )
            })
          }
        </Spin>
      </Box>
    )
  }
}

const mapStateToProps = ({ trade, markets }) => {
  return {
    trade,
    markets
  };
};

const mapDispatchToProps = ({ trade }) => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Trade);