  sendTransaction(web3, tx) {
    try {
      validator.validate({ type: "TX", value: tx });
    } catch (e) {
      return Promise.resolve(
        new Response(code.PARAM_INVALID.code, code.PARAM_INVALID.msg)
      );
    }

    // return new Promise(resolve => {
    //   web3.eth.sendTransaction(tx, function(err, transactionHash) {
    //     if (!err) {
    //       resolve({ result: transactionHash });
    //     } else {
    //       const errorMsg = err.message.substring(0, err.message.indexOf(" at "));
    //       resolve({ error: { message: errorMsg } });
    //     }
    //   });
    // });

    return new Promise(resolve => {
      window.dkeySendTxSuccess = function (resp) {
        console.info('dkey sendTransaction success', resp)
        resolve(resp);
      }
      window.dkeySendTxFailure = function (error) {
        console.info('dkey sendTransaction failure:', error)
        resolve({ error });
      }
      // let dkeyTxParams = {
      //   from: account,
      //   data: rawTx.data,
      //   gasLimit: rawTx.gasLimit,
      //   gasPrice: rawTx.gasPrice,
      //   to: rawTx.to,
      //   value: rawTx.value,
      //   nonce: rawTx.nonce,
      // }
      // window.ethereum.sign2(JSON.stringify(dkeyTxParams), 'dkeySignSuccess', 'dkeySignFailure')
      window.ethereum.sendTransaction(JSON.stringify(tx), 'dkeySendTxSuccess', 'dkeySendTxFailure')
    });
  }
