import React from 'react';
import { connect } from 'react-redux';
import numeral from 'numeral'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import { withRouter } from "react-router-dom"
import { Svg } from 'components'

class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
  }

  render() {
    let { history, markets: marketsStore, ticker: { dayInfo }, starMarket, tabs, contentSx = {}, fetchCurrentMarkets, updateAppState, ...restProps } = this.props
    let { currentMarket } = marketsStore
    let { variation } = dayInfo

    if (!currentMarket) return <></>
    let haveStar = _.find(marketsStore.stars, {market: currentMarket.market})
    return (
        <Flex alignItems="center" >
          <Flex alignItems="center" color="#505050">
            <Svg color="#505050" icon="iconliebiaozhankai" size=".18rem"></Svg>
            <Text ml=".04rem" fontSize="f3" lineHeight="lh3" fontWeight="semibold" onClick={() => {
              fetchCurrentMarkets()
              updateAppState({showMarketDrawer: true})
            }}>{currentMarket.baseToken}/{currentMarket.quoteToken}</Text>
            <Text ml={'s1'} px={'s2'} fontSize="f1" lineHeight="lh1"
              color={variation >= 0 ? 'c4' : 'c3'} bg={variation >= 0 ? 'rgba(80, 190, 140, 0.15)' : 'rgba(240, 80, 90, 0.15)'}
              sx={{borderRadius: '.03rem'}}>
              {numeral(variation).format('+0.00%')}
            </Text>
          </Flex>
          <Flex ml="s2" alignItems="center" onClick={() => {
            starMarket(currentMarket)
          }}>
            {/* <Svg color={haveStar ? '#0078FF' : '#c8c8c8'} icon="icon-Starlargegray" size=".17rem" /> */}
          </Flex>
        </Flex>
    )
  }
}

const mapStateToProps = ({markets, ticker}) => {
  return {
    markets, ticker
  };
};

const mapDispatchToProps = ({markets, appState}) => {
  return {
    starMarket: markets.starMarket,
    updateAppState: appState.update,
    fetchCurrentMarkets: markets.fetchCurrentMarkets
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Widget))