import _ from 'lodash'
import React, { forwardRef, useEffect, useState, useImperativeHandle, Fragment } from 'react';
import { withRouter } from "react-router-dom"
import { useIntl, FormattedMessage } from 'react-intl';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Button,
  Text
} from 'rebass'
import { connect } from 'react-redux'
import { Page, Svg } from 'components'
import { Trade, DepthHorizontal, TVChart, TabNav, TokenInfo } from 'widgets'
import { renderIf } from 'utils';
import api from 'api'


const Widget = ({
  history,
  markets
}) => {
  
  const intl = useIntl()
  let [activeTab, setActiveTab] = useState('depth')
  let [tokenInfo, setTokenInfo] = useState({})
  const tabs = [
    { text: intl.formatMessage({id: 'depth.title'}), value: 'depth' },
    { text: intl.formatMessage({id: 'trade.title'}), value: 'trade' },
    { text: intl.formatMessage({id: 'brief'}), value: 'intro' },
  ]
  useEffect (() => {
    let { currentMarket } = markets
    api.getContract(currentMarket.baseContractId).then(resp => {
      setTokenInfo(resp.data)
    })
  }, [])

  return (
    <Page height="unset">
      <TVChart />
      {/* <Flex px="mainPadding" py="mainPadding">
        <Depth mr="mainPadding" width="1.6rem" />
        <PlaceOrder ml="mainPadding" flex={1} />
      </Flex>
      <MyOrder flex={1} /> */}
      <TabNav tabs={tabs} onChange={tab => setActiveTab(tab.value)} />
      <Box pb=".35rem">
        {
          renderIf(activeTab === 'depth')(<DepthHorizontal/>)
        }
        {
          renderIf(activeTab === 'trade')(<Trade/>)
        }
        {
          renderIf(activeTab === 'intro')(<TokenInfo tokenInfo={tokenInfo} />)
        }
      </Box>
      <Flex sx={{
        position: 'fixed',
        bottom: 0,
        left: 0,
        right: 0,
        color: 'white',
        height: ".35rem",
        zIndex: 1
      }}>
        <Flex variant="flexCenter" flex={1} bg="c4" onClick={() => history.push({ pathname: `/`, data: { initialSide: 'BUY' } })}>
          <FormattedMessage id="buy" />
        </Flex>
        {/*  side  side  store  */}
        <Flex variant="flexCenter" flex={1} bg="c3" onClick={() => history.push({ pathname: `/`, data: { initialSide: 'SELL' } })}>
          <FormattedMessage id="sell" />
        </Flex>
      </Flex>
    </Page>
  )
}

const mapStateToProps = ({ markets }) => {
  return { markets }
}

const mapDispatchToProps = ({auth}) => ({
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Widget))